<template>
  <div class="activityDetail pt72">
    <div class="pageWidth">

      <Site :site="site"></Site>

      <div class="head">
        <img :src="data.act_imgs" alt="">
        <h3>{{data.act_name}}</h3>
        <div class="info">
          <p><Icon type="ios-time" size="16" />时间:{{data.time}}</p>
          <p><i class="iconfont icon-biaodiandidian"></i>地点：{{data.act_address}}</p>
          <p><Icon type="ios-call" size="20" />联系方式：{{data.act_phone}}</p>
          <p><Icon type="md-person" size="20" />主办方：{{data.host_name}}</p>
        </div>
        <ul class="price">
          <li :class="{active:index==choose}" @click="chooseFun(index)" v-for="p,index in data.price" :key="index">
            <b>{{p.desc}}</b>
            <span>￥{{p.price}}</span>
            <Icon type="md-checkmark" />
          </li>
        </ul>
        <div class="choose">
          <b>选择票数</b>
          <a href="javascript:;" @click="minus()"><Icon type="ios-remove" /></a>
          <input type="text" v-model="apply.number">
          <a href="javascript:;" @click="add()"><Icon type="ios-add" /></a>
        </div>
        <div class="btn">
          <button class="gray" v-if="choose == '-1'">报名参加</button>
          <button v-else @click="newPage()">报名参加</button>
        </div>
      </div>

      <Title title="活动详情"></Title>

      <div class="about">

        <div v-for="c,index in data.act_content" :key="c.id">
          <img v-if="c.type == 2" :src="c.content" alt="">
          <p v-else>{{c.content}}</p>
        </div>
      </div>

    </div>

  </div>
</template>

<script>

  import Vue from 'vue';
  import { Icon } from 'iview';
  Vue.component('Icon',Icon);

  import Title from '../block/Title'
  import Site from '../block/Site'

  // 数据获取；
  import request from '../../../static/js/request'
  Vue.use(request);

  export default {
    name: "ActivityDetail",
    components:{
      Title ,Site
    },
    data(){
      return{
        data:'',
        d:{
          'id':this.$route.query.id
        }
        ,site:[
          {
            title:'活动列表',
            src:'/activity'
          },
          {
            title:'活动详情',
            src:''
          }
        ]
        ,choose:-1
        ,apply:{
          price:'',
          number:1,
          title:'',
          id:this.$route.query.id
        }
      }
    }
    ,methods:{
      minus(){
        this.apply.number = this.apply.number-- < 2 ? 1 : this.apply.number--;
      }
      ,add(){
        this.apply.number++;
      }
      ,chooseFun(idx){
        this.choose = idx;
        this.apply.title = this.data.price[idx].desc;
        this.apply.price = this.data.price[idx].price;
      }
      , newPage(){
        console.log(this.apply);
        this.$router.push({ name:'activityPay' ,params:{id:this.apply} })
        // const old = this.$router.resolve({
        //   path: 'activityPay',
        //   query: {
        //     id:this.d.id
        //   }
        // });
        // window.open(old.href, '_blank')
      }
    }
    ,created(){
      var _this = this;
      console.log(_this.d);
      request.getDataPackage('Activity/getActivityInfo',_this.d,function (data) {
        console.log(data);
        _this.$set(_this , 'data' ,data.data );
      });
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../static/scss/public.scss";

  .activityDetail{
    padding-bottom: 100px;
  }
  .head{
    position: relative;
    margin-top: 30px;
    padding-left: 800px;
    min-height: 350px;
    font-family:MicrosoftYaHei;
    margin-bottom: 60px;
    
    img{
      width:768px;
      height:350px;
      position: absolute;
      left: 0;
      top: 0;
    }
    h3{
      font-size:23px;
      font-weight:400;
      color:rgba(51,51,51,1);
      margin-bottom: 20px;
    }
    .info{
      font-size:15px;
      font-weight:400;
      color:rgba(70,70,70,1);

      .icon-biaodiandidian{
        font-size: 20px;
        margin-left: -2px;
      }
      p{
        margin-bottom: 5px;
        position: relative;
        padding-left: 30px;
      }
      i{
        color: $main-color;
        position: absolute;
        left: 0;
        top: 50%;transform: translateY(-50%);
      }
    }

    .price{
      margin-top: 20px;

      li{
        width:220px;
        line-height: 40px;
        margin-right: 20px;
        display: inline-block;
        margin-bottom: 10px;
        border:1px solid rgba(157,157,157,1);
        font-size:15px;
        font-family:MicrosoftYaHei;
        padding:0 10px;
        font-weight:400;
        color:rgba(70,70,70,1);
        cursor: pointer;
        height: 40px;

        i{
          display: none;
          position: absolute;
        }

        b{
          width: 126px;
          overflow: hidden;
          display: inline-block;
          white-space: nowrap;
          text-overflow: ellipsis;
        }

        &.active{
          border-color: $main-color;
          position: relative;

          i{
            display: block;
            right: -1px;
            bottom: -1px;
            color: #fff;z-index: 1;
            font-size: 10px;
          }

          &:after{
            position: absolute;
            content: '';
            border-top: 18px solid transparent;
            border-left: 18px solid transparent;
            border-bottom: 18px solid $main-color;
            right: 0;
            bottom: 0;
          }
        }
        span{
          float: right;
        }
      }
    }

    .choose{
      margin-bottom: 20px;
      line-height: 22px;
      margin-top: 5px;

      b{
        font-size: 17px;
        margin-right: 22px;
      }
      input{
        width: 40px;
        border: none;
        text-align: center;
      }
      a{
        width:22px;
        height:22px;
        border:1px solid rgba(157,157,157,1);
        display: inline-block;
        text-align: center;

        i{
          font-size: 20px;
          margin-top: -3px;
        }
      }
    }
    .btn{

      button{
        width:210px;
        line-height:40px;
        text-align: center;
        border: none;
        font-size:15px;
        background:rgba(39,135,244,1);
        color: #fff;
        cursor: pointer;

        &.gray{
          background: #999;
          cursor: default;
        }
      }
    }

  }
  .about{
    margin-top: 30px;

    img{
      max-width: 50%;
      display: block;
      margin: auto;
    }
    p{
      padding: 20px 0;
      font-size:17px;
      font-weight:400;
      color:rgba(70,70,70,1);
      line-height:24px;
    }
  }
</style>



