<template>
  <div class="myNeed">

    <div class="write">
      <textarea placeholder="发布您的需求，寻找更多合作" name="" id="" cols="30" rows="10"></textarea>
      <h4>添加图片 <span>（最多九张）</span></h4>


      <UploadTest class="addDiv"></UploadTest>


      <h4>请填写需求标签 <span></span></h4>
      <div class="need-label">
        <input type="text" v-model="label[0]" v-on:input="inputChange(0)">
        <input type="text" v-model="label[1]" v-on:input="inputChange(1)">
        <input type="text" v-model="label[2]" v-on:input="inputChange(2)">
      </div>
      <div class="btn">
        <button>发布</button>
      </div>
    </div>



    <div class="list" v-if="list.lis.length">
      <ul>
        <li @click="newPage(l.id)" v-for="l ,index in list.lis" :key="index">
          <h2 v-if="l.demand_content">{{l.demand_content}}</h2>
          <span class="time">{{l.time}}</span>
          <div class="pic" v-if="l.image.length">
            <span v-for="i,index in l.image" :key="i.id" :style="{ 'background-image': 'url(' + i.img_url + ')'}"></span>
            <b v-if="l.image.length>3">+{{l.image.length-3}}</b>
          </div>
          <div class="info">
            <p>
              <a href="">删除</a>
              <b>{{l.member_alex}}</b>
            </p>
            <p><i class="iconfont icon-liuyan"></i>{{l.cmt_num}}</p>
          </div>
        </li>
      </ul>

      <Page @on-change="onChange" class="pageNext gray" :current="d.page" :total="list.total" :page-size="d.limit"  prev-text="上一页" next-text="下一页" />
    </div>

  </div>
</template>

<script>
  import Vue from 'vue';
  import { Page } from 'iview';
  Vue.component('Page',Page);

  // 数据获取；
  import request from '../../../static/js/request'
  Vue.use(request);

  import UploadTest from '../block/UploadTest'

  export default {
    name: "MyNeed"
    ,data(){
      return{
        list:{
          lis:'',
          total:1
        },
        site:[
          {
            title:'需求',
            src:''
          }
        ],
        d:{
          'limit':10,
          'page': 1
        },
        label:['' ,'' ,'']
        ,toke:''

        ,defaultList: [],
        imgName: '',
        visible: false,
        uploadList: []
      }
    },
    methods:{

      uploader(){

      },
      inputChange(idx){
        // if(this.label[idx].length>5){
        //   this.label[idx] = this.label[idx].substr(0,5)
        // }
      },
      onChange(e){
        var _this = this;
        _this.$set(_this.d , 'page' ,e*1 );
        request.getDataPackage('Demand/myDemand',_this.d,function (data) {
          _this.$set(_this.list , 'lis' ,data.data.list.list );
        });
      }
      , newPage(id){
        const old = this.$router.resolve({
          path: 'needDetail?id='+id
        });
        console.log(old);
        window.open(old.href, '_blank')
      }
    },
    created(){
      var _this = this;
      request.getDataPackage('Demand/myDemand',_this.d,function (data) {
        console.log(data  ,data.data.list.count);
        _this.$set(_this.list , 'lis' ,data.data.list.list );
        if(data.data.list.count){
          _this.$set(_this.list , 'total' ,data.data.list.count*1 );
        }
      });
    }
    ,components:{
      UploadTest
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../static/scss/public.scss";

  .write{
    background:rgba(255,255,255,1);
    padding: 30px;
    margin-bottom: 30px;
    overflow: hidden;

    .addDiv{
      margin-bottom: 22px;
      margin-top: 5px;
    }

    textarea{
      background: #F4F4F4;
      border: none;
      width: 100%;
      height: 96px;
      padding: 10px;
      font-size: 19px;
      margin-bottom: 20px;
    }
    h4{
      font-size:19px;
      font-weight:400;
      color:rgba(103,103,103,1);
      padding-bottom: 10px;

      span{
        font-size:11px;
        font-weight:400;
        color:rgba(254,124,124,1);
      }
    }
    .need-label{
      display: flex;
      float: left;

      input{
        width:122px;
        height:36px;
        background:rgba(244,244,244,1);
        border: none;
        padding:0 10px;
        text-align: center;
        font-size:17px;
        margin-right: 20px;
      }
    }
    .btn button{
      float: right;
      width:106px;
      height:44px;
      font-size:15px;
      font-family:MicrosoftYaHei;
      font-weight:400;
      color:rgba(255,255,255,1);
      background: $main-color;
      border: none;
      cursor: pointer;
    }
  }

  .list{
    background:rgba(255,255,255,1);
    margin-bottom: 88px;

    ul{
      padding-top: 10px;

      li{
        border-bottom: 1px solid #F4F4F4;
        position: relative;
        margin:20px 32px 0 47px;
        padding-bottom: 20px;

        &:hover{
          cursor: pointer;

          &:after{
            background: $main-color;
          }
        }

        &:after{
          position: absolute;
          content: '';
          width:7px;
          height:7px;
          background:rgba(196,196,196,1);
          @include border-radius(50%);
          left: -20px;
          top: 8px;
        }

        h2{
          padding-right: 140px;
          font-size: 17px;
          color: #464646;
          margin-bottom: 20px;
          display: -webkit-box;overflow: hidden;text-overflow: ellipsis;word-wrap: break-word;white-space: normal!important;-webkit-line-clamp: 2;-webkit-box-orient: vertical;

          &:hover{
            color: $main-color;
          }
        }
        .time{
          color: #9D9D9D;
          font-size: 13px;
          position: absolute;
          right: 0;
          top: 0;
        }
        .pic{
          position: relative;
          display: inline-block;

          span{
            width: 90px;
            height: 90px;
            display: inline-block;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center;
            margin-right: 20px;

            &:nth-child(3){
              margin-right: 0;
            }
          }
          b{
            line-height:22px;
            background:rgba(23,24,27,.4);
            font-size: 15px;
            color: #fff;
            position: absolute;
            right: 0;
            bottom: 5px;
            padding:0 10px;
          }
        }
        .info{
          display: flex;
          justify-content: space-between;

          p{

            a{
              color: #9D9D9D;
              font-size: 13px;

              &:hover{
                color: $main-color;
              }
            }

            &:nth-child(2){
              width: 66px;
            }
          }

          img{
            width:30px;
            height:30px;
            @include border-radius(50%);
            vertical-align: middle;
            margin-right: 13px;
            border: 1px solid #eee;
          }
          b{
            font-size:13px;
            font-family:MicrosoftYaHei;
            font-weight:400;
            line-height: 30px;
            display: inline-block;
            color:rgba(157,157,157,1);
          }
          i{
            color: #9D9D9D;vertical-align: middle;
            margin-right: 8px;
          }
        }
      }
    }


    .pageNext{
      text-align: center;
      padding-bottom: 120px;
      margin-top: 60px;

    }

    .ivu-page > .ivu-page-item{
      background: #f4f4f4 !important;
    }

  }

</style>



