<template>
  <div class="goodsOne" @click="newPage()">
    <a>
      <img v-if="data.goods_img" :src="data.goods_img" :alt="data.goods_name">
      <img v-if="data.image[0]" :src="data.image[0]" :alt="data.goods_name">
    </a>
    <p>{{data.goods_name}}111</p>
    <div class="tag">
      <Tag color="gold" v-for="l,index in data.goods_tag" :key="index">{{l}}</Tag>
      <Icon title="加入清单" custom="iconfont icon-jiahao" size="30" color="#214EF7"></Icon>
    </div>
  </div>
</template>

<script>

  import Vue from 'vue';
  import { Tag ,Icon } from 'iview';
  Vue.component('Tag',Tag);
  Vue.component('Icon',Icon);

  export default {
    name: "GoodsOne",
    props:{
      data:''
    },
    methods:{
      newPage(){
        const old = this.$router.resolve({
          path: 'goodDetail',
          query: {
            id:this.data.goods_id
          }
        });
        // console.log(this.data.company_id);
        window.open(old.href, '_blank')
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../static/scss/public.scss";

  .goodsOne{
    width:328px;
    background: #fff;
    overflow: hidden;

    &:hover{

      img{
        transform: scale(1.3);
      }
      p{
        color: $main-color;
      }
    }
    a{
      position: relative;
      height:219px;
      overflow: hidden;
      display: block;


      img{
        position: absolute;
        width:100%;
        height:100%;
        transform: scale(1.0);
        transition: all .3s;
      }
    }
    p{
      display: -webkit-box;overflow: hidden;text-overflow: ellipsis;word-wrap: break-word;white-space: normal!important;-webkit-line-clamp: 1;-webkit-box-orient: vertical;
      margin: 10px 0 20px 0;
      font-size: 15px;
      padding-left: 10px;
    }
    .tag{
      margin-bottom: 12px;
      padding-left: 10px;

      i{
        float: right;
        margin-right: 10px;
        cursor: pointer;
      }
    }
  }

</style>



