<template>
  <li>
    <a><img :src="data.case_image" alt=""></a>
    <h3>{{data.case_name}}</h3>
    <div class="tag">
      <p v-for="t,index in data.case_trait" :key="index">{{t}}</p>
    </div>

    <!--隐藏更多内容-->
    <div class="goodList-detail" v-if="showUp">
      {{data.case_details.substr(0,300)}}
      ...
      <span class="unfold" @click="down()">展开 <Icon type="ios-arrow-down" /></span>
    </div>
    <!--显示所有内容-->
    <div class="goodList-detail" v-else>
      {{data.case_details}}
      <span class="unfold" v-if="up" @click="ups()">收起 <Icon type="ios-arrow-up" /></span>
    </div>

  </li>
</template>

<script>
  export default {
    name: "GoodsOneLang"
    ,data(){
      return{
        showUp:true
        ,up:false
      }
    }
    ,props:{
      data:''
    }
    ,methods:{
      down(){
        var _this = this;
        _this.$set(_this , 'up' ,true );
        _this.$set(_this , 'showUp' ,false );
      }
      ,ups(){
        var _this = this;
        _this.$set(_this , 'showUp' ,true );
      }
    }
    ,created(){
      var _this = this;
      if(_this.data.case_details.length>300){
        _this.$set(_this , 'showUp' ,true );
      }else {
        _this.$set(_this , 'showUp' ,false );
        _this.$set(_this , 'up' ,false );
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../static/scss/public.scss";

  .unfold{
    color: $main-color;
    cursor: pointer;
  }
  li{
    min-height: 220px;
    padding: 0 20px 20px 330px;
    position: relative;

    &:hover{
      color:rgba(39,135,244,1);
      background: #F4F4F4;
    }

    img{
      width:260px;
      height:195px;
      position: absolute;
      left: 40px;
      top: 15px;
    }
    h3{
      font-size:19px;
      font-weight:bold;
      line-height:24px;
      padding: 30px 0 20px;
      min-height: 40px;
    }
    .tag{
      margin-bottom: 20px;
      font-size: 17px;
    }

    .goodList-detail{
      font-size: 15px;
      color: #9D9D9D;
    }

  }

</style>



