<template>
  <div class="title">
    <div class="left">
      {{title}}
      <span>{{info}}</span>
    </div>
    <div class="right" v-if="more">
      <a @click="newPage(link)">
        {{more}}
        <Icon custom="iconfont icon-jiantou_xiangyou" size="20" />
      </a>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue';
  import { Icon } from 'iview';
  Vue.component('Icon',Icon);

  export default {
    name: "Title",
    data(){
      return{

      }
    },
    props:{
      title:'',
      more:'',
      link:'',
      info:''
    },
    methods:{
      newPage(link){
        const old = this.$router.resolve({
          path: link
        });
        console.log(old);
        window.open(old.href, '_blank')
      }
    }
  }
</script>

<style scoped lang="scss">
  .title{
    display: flex;
    justify-content: space-between;
    align-items: center;

    .left{
      font-size:23px;
      color:rgba(51,51,51,1);
      position: relative;
      padding-left: 9px;

      &:after{
        position: absolute;
        content: '';
        width:4px;
        height:28px;
        background:rgba(39,135,244,1);
        left: -4px;
        top: 50%;
        transform: translateY(-50%);
      }
      span{
        font-size:15px;
        color:rgba(103,103,103,1);
        display: inline-block;
        margin-left: 40px;
      }
    }
    .right{
      font-size:13px;
      color:rgba(53,163,247,1);
      cursor: pointer;
    }
  }
</style>



