<template>
  <!--:class="{ hide: isHide }"-->
  <div id="app" >
    <Header v-on:login="loginFun"></Header>
    <router-view></router-view>
    <Footer></Footer>
    <div :class="[{'shows':is },'top']">
      <Icon @click="backTop" custom="iconfont icon-jiantou" size="34" />
    </div>
  </div>
</template>

<script>
  import Header from './components/head/Header'
  import Content from './components/index/Contents'
  import Footer from './components/foot/Footer'

  import Vue from 'vue';
  import { Icon } from 'iview';
  Vue.component('Icon',Icon);

  export default {
    name: 'App',
    components:{
      Header,
      Content,
      Footer
    }
    ,data(){
      return{
        is:false,
        isHide:false
      }
    }
    ,methods:{
      handleScroll () {
        var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
        if(scrollTop>400){
          this.is = true;
        }else {
          this.is = false;
        }
      },
      backTop(){
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0
      },
      loginFun(child){
        // this.isHide = child;
        this.$set(this , 'isHide' ,child);
        console.log(child ,this.isHide)
      }
    }
    ,mounted () {
      window.addEventListener('scroll', this.handleScroll)
    }

  }
</script>

<style lang="scss">
  @import "../static/scss/public.scss";
  #app {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  .hide{
    overflow: hidden;
    height: 100vh;
  }
  .top{
    width:70px;
    height:70px;
    background:rgba(255,255,255,1);
    position: fixed;
    /*left: 50%;*/
    /*margin-left: 700px;*/
    right: 50px;
    bottom: 50px;
    text-align: center;
    line-height: 70px;
    color: $main-color;
    cursor: pointer;
    opacity: 0;
    transition: all .3s;
    
    &:hover{
      background: $main-color;
      color: #fff;
    }

    &.shows{
      opacity: 1;
    }
  }
</style>



