<template>
  <div class="banner">
    <Carousel v-model="value2" :loop="loop" :autoplay="autoPlay" :autoplay-speed="autoPlaySpeed">
      <CarouselItem v-for="d ,index in data" :key="index">
        <div class="carousel" :style="{'background-image': 'url(' + d.ad_img +')'}"></div>
      </CarouselItem>
    </Carousel>
  </div>
</template>

<script>

  import Vue from 'vue'
  import { Carousel ,CarouselItem } from 'iview'
  Vue.component('Carousel' ,Carousel);
  Vue.component('CarouselItem' ,CarouselItem );


  export default {
    name: "Banners",
    data () {
      return {
        value2: 0
      }
    },
    props:{
      data:'',
      loop:'',
      autoPlay:'',
      autoPlaySpeed:''
    }
  }

</script>

<style scoped lang="scss">
  .banner .carousel{
    height: 360px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position:center 0;

    img{
      width: 100%;
    }
  }
</style>



