<template>
    <div class="my pt72">

      <div class="pageWidth">

        <div class="left">

          <div class="headPhoto">
            <a href=""><img src="http://cdn.vr.xiaoqihui.com/2019-01-29/66648.jpg" alt=""></a>
            <p>那么</p>
            <div class="com-name">上海校企荟信息科技有限公司</div>
            <div class="btn">
              <button @click="newPage()">企业认证</button>
              <button class="btn_green">入驻审核中</button>
              <button @click="newPage2()">申请入驻</button>
            </div>
          </div>

          <MyMenu></MyMenu>

        </div>


        <div class="right">
          <router-view></router-view>
        </div>

      </div>
    </div>
</template>

<script>

  import MyMenu from '../my/MyMenu'

    export default {
        name: "My"
      ,components:{
        MyMenu
      }
      ,methods:{
        newPage(){
          const old = this.$router.resolve({
            path: 'enterpriseCertification',
            query: {
              id:'123'
            }
          });
          console.log(old);
          window.open(old.href, '_blank')
        }
        , newPage2(){
          const old = this.$router.resolve({
            path: 'enterprises',
            query: {
              id:'123'
            }
          });
          console.log(old);
          window.open(old.href, '_blank')
        }
      }
    }
</script>

<style scoped lang="scss">
  @import "../../../static/scss/public.scss";

  .my{
    background: #f4f4f4;

    .pageWidth{
      padding-top: 50px;
      display: flex;
      justify-content: space-between;
    }
  }
  .left{
    width: 250px;
    margin-bottom: 30px;

    .headPhoto{
      background: #fff;
      text-align: center;
      min-height: 330px;

      a{
        display: inline-block;
        border: 1px solid #ccc;
        width:96px;
        height:96px;
        @include border-radius(8px);
        overflow: hidden;
        vertical-align: middle;
        text-align: center;
        margin: 30px 0 20px;
        position: relative;
      }
      img{
        position: relative;
        top: 50%;transform: translateY(-50%);
        max-width: 100%;
        max-height: 100%;
      }
      p{
        font-size: 15px;
        color: #333;
      }
      .com-name{
        position: relative;
        margin:6px 20px 0 30px;
        text-align: left;
        padding-left: 15px;
        @extend .text-overflow;

        &:after{
          width:6px;
          height:6px;
          background:rgba(39,135,244,1);
          @include border-radius(50%);
          left: 0;
          position: absolute;
          content: '';
          top: 50%;
          transform: translateY(-50%);
        }
      }
      .btn button{
        margin-top: 60px;
        width:193px;
        height:44px;
        font-size:15px;
        font-family:MicrosoftYaHei;
        font-weight:400;
        color:rgba(255,255,255,1);
        background: $main-color;
        border: none;
        cursor: pointer;
      }
    }

    .myMenu{
      margin-top: 30px;
    }

  }
  .right{
    width: 1080px;
  }

</style>



