<template>
  <div class="down pt72">
    <div class="pageWidth content">

      <div class="font">
        上海校企荟信息科技有限公司（HUI）简称校企荟。
        精通目前互联网主流技术及可视化数据类技术整合与人工智能领域的技术。
        公司愿景是让教育资源无边界，让更多优秀的、创新的产品服务于学校和学生，
        推动教育创新和资源对接。
      </div>

      <div class="share">
          <span>
            <img src="../assets/images/ewm.png" alt="">
            <img src="../assets/images/logo-new.png" alt="">
          </span>
        <p>
          扫码下载校企荟APP
        </p>
      </div>

    </div>

  </div>
</template>

<script>
  export default {
    name: "Down"
  }
</script>

<style scoped lang="scss">
  @import "../../static/scss/public.scss";

  .down{
    padding-bottom: 100px;

    .content{
      padding-left: 100px;
      margin-top: 90px;
      min-height: 630px;
      background: url("../assets/images/down.png") no-repeat center 0;
    }

    .font{
      padding-top: 120px;
      margin-bottom: 50px;
      width:514px;
      font-size:15px;
      font-family:PingFang-SC-Regular;
      font-weight:400;
      color:rgba(51,51,51,1);
      line-height:24px;
    }

    .share{
      width: 140px;

      span{
        position: relative;
        display:block;

        img:nth-child(1){
          width: 100%;
          -webkit-box-shadow:2px 2px 15px #ccc;
          -moz-box-shadow: 2px 2px 15px #ccc;
          box-shadow: 2px 2px 15px #ccc;
        }
        img:nth-child(2){
          width: 60px;
          position: absolute;
          left: 50%;
          top: 50%;transform: translateX(-50%) translateY(-50%);
          border: 2px solid #fff;
          @include border-radius(14px);
        }
      }

      p{
        text-align: center;
        font-size:15px;
        font-weight:400;
        color:#333333;
        margin-top: 6px;
      }
    }

  }

</style>



