<template>
    <div class="companyOne">
      <a @click="newPage()"><img :src="data.com_logo" :alt="data.com_name"></a>
      <p>{{data.com_name}}</p>
    </div>
</template>

<script>
  export default {
    name: "CompanyOne",
    props:{
      link:'',
      data:''
    },
    methods:{
      newPage(){
        const old = this.$router.resolve({
          path: 'companyDetail',
          query: {
            id:this.data.company_id
          }
        });
        // console.log(this.data.company_id);
        window.open(old.href, '_blank')
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../static/scss/public.scss";

  .companyOne{
    width:205px;

    &:hover{

      img{
        transform: scale(1.3);
      }
      p{
        color: $main-color;
      }
    }
    a{
      position: relative;
      height:205px;
      overflow: hidden;
      display: block;
      background: #fff;

      img{
        position: absolute;
        width:205px;
        height:205px;
        transform: scale(1.0);
        transition: all .3s;
      }
    }
    p{
      display: -webkit-box;overflow: hidden;text-overflow: ellipsis;word-wrap: break-word;white-space: normal!important;-webkit-line-clamp: 1;-webkit-box-orient: vertical;
      margin: 12px 0 0 0;
      text-align: center;
      font-size: 15px;

    }
  }

</style>



