<template>
  <div class="messageOne">
    <img :src="data.author.member_header" alt="">
    <h3>
      {{data.author.member_alex}}
      <span>{{data.ct_time}}</span>
    </h3>
    <p class="font">
      {{data.content}}
    </p>
    <div class="childMessage" v-if="data.reply.length">
      <ul>
        <li v-for="r,index in data.reply" :key="r.id">
          <h4>
            {{r.reply_author.member_alex}} <b>回复</b> {{r.reply_to}}
            <span>{{r.ct_time}}</span>
          </h4>
          <p>{{r.reply_content}}</p>
        </li>
      </ul>
    </div>
    <div class="message">
      <p>
        <input type="text" placeholder="发表评论" v-model="replyArr.content">
      </p>
      <a href="javascript:;" @click="reply()">评 论</a>
    </div>
    <p class="reply"><a href="">回复</a></p>
  </div>
</template>

<script>

  import Vue from 'vue';

  // 数据获取；
  import request from '../../../static/js/request'
  Vue.use(request);

  export default {
    name: "MessageOne"
    ,data(){
      return{
        replyArr:{
          content:'',
          type_id:'',
          type:7,
          cmt_id:'',
          to_member_id:''
        }
      }
    },
    props:{
      data:''
    }
    ,methods:{

      reply(){
        console.log(this.replyArr);

        var _this = this;
        request.getDataPackage('Demand/addComment',_this.replyArr,function (data) {
          console.log(data);


        });

      }

    }
  }
</script>

<style scoped lang="scss">
  @import "../../../static/scss/public.scss";

  .message{
    margin-top: 20px;
  }

  .messageOne{
    position: relative;
    padding-left: 80px;
    margin-bottom: 24px;
    padding-bottom: 30px;

    img{
      width:60px;
      height:60px;
      @include border-radius(8px);
      position: absolute;
      left: 0;
      top: 0;
    }
    h3{
      line-height: 60px;
      margin-bottom: 10px;
      font-size:17px;

      span{
        float: right;
      }
    }
    .font{
      font-size:17px;
      font-family:MicrosoftYaHei;
      font-weight:400;
      color:rgba(70,70,70,1);
      line-height:24px;
    }
    .reply{
      font-size:15px;
      font-family:MicrosoftYaHei;
      font-weight:400;
      color:rgba(39,135,244,1);
      text-align: right;
      padding-top: 8px;

    }
  }

  .childMessage{
    background:rgba(248,248,248,1);
    padding-bottom: 10px;

    ul{
      margin: 10px;

      li{
        border-bottom: 1px solid #EAEAEA;
        padding-bottom: 10px;

        &:last-child{
          border: none;
        }

        h4{
          font-size:13px;
          color: #9D9D9D;
          padding: 20px 0 0;

          span{
            float: right;
          }
        }
        p{
          font-size:13px;
          font-family:MicrosoftYaHei;
          font-weight:400;
          color:rgba(103,103,103,1);
          line-height:24px;
        }
      }
    }
  }
</style>



