<template>
  <div class="companyDetail pt72">
    <div class="pageWidth">

      <Site :site="site"></Site>
      <Title title="企业详情"></Title>

      <div class="detailHead">
        <img :src="data.com_logo" alt="">
        <h3>{{data.com_name}}</h3>
        <div class="tag">
          <Tag v-for="t,index in data.com_cat_name" :key="index" color="blue">{{t}}</Tag>
        </div>
        所在区域:{{data.com_address}}
        <p class="tel" v-if="tel"><Icon type="ios-call" size="26" />{{tel}}</p>
      </div>
      <div class="mainAbout">
        <span class="subTit">主营业务：</span>{{data.com_service}}
      </div>
      <div class="detailInfo">
        {{data.com_info}}
      </div>

      <Title title="企业产品"></Title>

      <div class="goodList">
        <ul>
          <li v-for="g ,index in data.goods_list" :key="index">
            <a><img :src="g.image[0]" alt=""></a>
            <h3>{{g.goods_name}}</h3>
            <div class="tag">
              <Tag v-for="t,index in g.goods_tag" :key="index" color="gold">{{t}}</Tag>
            </div>
            <a class="plus" href=""><Icon type="md-add" />添加到产品清单</a>
          </li>
        </ul>
      </div>

    </div>
  </div>
</template>

<script>

  import Vue from 'vue';
  import { Tag } from 'iview';
  Vue.component('Tag',Tag);

  import Title from '../block/Title'
  import Site from '../block/Site'

  // 数据获取；
  import request from '../../../static/js/request'
  Vue.use(request);

  export default {
    name: "CompanyDetail",
    components:{
      Title ,Site
    },
    data(){
      return{
        data:'',
        tel:'',
        d:{
          'company_id':this.$route.query.id
        }
        ,site:[
          {
            title:'企业列表',
            src:'/company'
          },
          {
            title:'企业详情',
            src:''
          }
        ]
      }
    },
    created(){
      var _this = this;
      console.log(_this.d);
      request.getDataPackage('Company/getCompanyInfo',_this.d,function (data) {
        console.log(data);
        _this.$set(_this , 'data' ,data.data );
        _this.$set(_this , 'tel' ,data.data.contact_list[0].mobile );
        // if(data.data.count){
        //   _this.$set(_this.list , 'total' ,data.data.count*1 );
        // }
      });
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../static/scss/public.scss";

  .detailHead{
    padding: 30px 50px 30px 200px;
    margin-top: 30px;
    background: #F4F4F4;
    font-size:13px;
    color:rgba(70,70,70,1);
    line-height:21px;
    position: relative;
    min-height: 178px;

    img{
      width: 120px;
      height: 120px;
      position: absolute;
      left: 50px;
      top: 30px;
    }
    h3{
      font-size:21px;
      font-weight:bold;
      color:rgba(51,51,51,1);
      line-height:32px;
      margin-top: 13px;
    }
    .tag{
      padding:10px 0;

      .ivu-tag{
        margin-right: 16px;
      }
    }
    .tel{
      display: inline-block;
      background: #2787F4;
      color: #fff;
      padding:0 16px;
      font-size: 17px;
      position: absolute;
      right: 50px;
      bottom: 30px;
      line-height: 44px;

      .ivu-icon{
        margin-right: 10px;
      }
    }
  }
  .mainAbout{
    margin: 40px;
    position: relative;
    padding-left: 90px;
    font-size:17px;
    color:rgba(70,70,70,1);
    line-height:24px;

    .subTit{
      position: absolute;
      left: 0;
      font-weight:bold;
    }
  }
  .detailInfo{
    font-size:17px;
    font-weight:400;
    color:rgba(70,70,70,1);
    line-height:24px;
    margin: 40px;
    white-space: pre-wrap;
  }

  .goodList{
    margin-top: 20px;
    margin-bottom: 200px;

    ul{

      li{
        min-height: 220px;
        padding: 20px 20px 20px 320px;
        position: relative;

        &:hover{
          color:rgba(39,135,244,1);
          background: #F4F4F4;
          cursor: pointer;
        }
        
        img{
          width:270px;
          height:180px;
          position: absolute;
          left: 20px;
          top: 20px;
        }
        h3{
          font-size:19px;
          font-weight:bold;
          line-height:24px;
          margin: 10px 0 20px;
          min-height: 40px;
        }
        .tag{
          margin-bottom: 30px;

          .ivu-tag{
            margin-right: 10px;
          }
        }
        .plus{
          line-height: 30px;
          background: #214EF7;
          color: #fff;
          display: inline-block;
          padding: 0 7px;
          font-size: 15px;

          i{
            font-size: 26px;
            margin-right: 10px;
          }
        }
        
      }
    }
  }

</style>



