<template>
  <div class="uploadImg">

    <img v-for="i,index in defaultList" :key="index" :src="i.url" alt="">
    <!--<Icon type="ios-trash-outline" @click.native="handleRemove(i)"></Icon>-->
    <Upload
      ref="upload"
      :max-size="1024"
      :show-upload-list="false"
      :format="['jpg','jpeg','png']"
      :default-file-list="defaultList"
      :before-upload="handleUpload"
      :on-format-error="handleFormatError"
      :on-exceeded-size="handleMaxSize"
      :on-success="handleSuccess"
      action="//up.qiniup.com/"
      :data="{ token: toke ,key:imgName }"
    >
      <!---->
      <div class="add-pic" v-if="maxN">
        <a href="javascript:;"><i class="iconfont icon-jia"></i></a>
      </div>
    </Upload>
  </div>
</template>
<script>

  import Vue from 'vue';
  import { Upload } from 'iview';
  Vue.component('Upload',Upload);

  // 数据获取；
  import request from '../../../static/js/request'
  Vue.use(request);


  export default {
    name: "UploadImage",
    data () {
      return {
        defaultList: [] // 总文件List
        ,uploadFile: [] // 需要上传的文件List
        ,url:'http://cdn.vr.xiaoqihui.com/'
        ,toke:''
        ,imgName:''
        ,maxN:true
      }
    },
    methods: {
      handleRemove (file) {
        const fileList = this.$refs.upload.fileList;
        this.$refs.upload.fileList.splice(fileList.indexOf(file), 1);
      },
      handleUpload (file) {
        console.log('handleUpload'+file);

        var fileN = file.name.split('.');

        var date = new Date();
        var year = date.getFullYear();
        var month = date.getMonth() + 1;
        var strDate = date.getDate();
        if (month >= 1 && month <= 9) {
          month = "0" + month;
        }
        if (strDate >= 0 && strDate <= 9) {
          strDate = "0" + strDate;
        }
        var m = Math.random()*10000 + Date.now();
        var key = year.toString() + month.toString() + strDate.toString() + '/' + Math.ceil(m) + "." + fileN[fileN.length-1];

        this.imgName = key;

        // file['key'] = key;
        // file['token'] = this.toke;
        // console.log('file',file);
        // this.uploadFile.push(file);

        var _this = this;

        setTimeout(function () {
          // for (let i = 0; i < _this.uploadFile.length; i++) {
          //   let item = _this.uploadFile[i];
          //   console.log('item',item);
            _this.$refs.upload.post(file);
          // }
        },200);
        return false;


      },
      upload2(){
        console.log('upload () {')
      },
      handleFormatError (file) {
        console.log('handleFormatError', file)
      },
      handleMaxSize (file) {
        console.log('handleMaxSize', file)
      },
      handleSuccess (res, file ,filelist) {
        console.log('handleSuccess' ,res, file);
        file['url'] = this.url +res.key;
        this.defaultList.push(file);

        console.log(this.defaultList);

        if(this.defaultList.length>8){
          this.maxN = false;
        }

      }
    },
    computed:{
      // imgName(){
      //   return ''
      // }
    },
    mounted () {

    },
    created(){
      let _this = this;
      request.getDataPackage('Home/getQiniuToken','',function (data) {
        console.log(data.data);
        _this.$set(_this , 'toke' ,data.data );
      });
    }

  }
</script>

<style scoped lang="scss">
  @import "../../../static/scss/public.scss";

  .ivu-upload{
    display: inline-block;
  }
  .uploadImg{
    overflow: hidden;
    margin-bottom: 15px;

    img{
      width:70px;
      height:70px;
      margin-right: 15px;
      margin-bottom: 15px;
      float: left;

      &:nth-child(5n){
        margin-right: 0;
      }
    }
  }

  .add-pic{
    margin-bottom: 30px;
    float: left;

    a{
      width:70px;
      line-height:70px;
      height:70px;
      overflow: hidden;
      background:rgba(244,244,244,1);
      text-align: center;
      display: inline-block;
      color: #9D9D9D;

      i{
        font-size: 30px;
      }
    }
  }
</style>



