<template>
    <div class="login" v-if="showLogin">
      <div class="bg"></div>
      <div class="login-c">
        <a class="close" @click="close()" href="javascript:;"><i class="iconfont icon-guanbi"></i></a>
        <div class="logo">
          <img src="../../assets/images/logo-s.png" alt="">
        </div>
        <ul>
          <li>
            <b>手机号</b>
            <span><input type="text" v-model="d.phone" placeholder="请输入手机号"></span>
          </li>
          <li>
            <b>验证码</b>
            <span><input type="text" v-model="d.code" placeholder="请输入验证码"></span>
            <a @click="getNub()" v-if="isClickYzm" class="yzm" href="javascript:;">获取验证码</a>
            <a v-else class="yzm unclick" href="javascript:;">{{time}}秒后可重新获取</a>
          </li>
          <li>
            <div class="tip">{{tip}}</div>
            <i @click="chooseF()" :class="[{ choose: isChoose }, 'unchoose']"></i>
            <a href="javascript:;" class="xy">同意校企荟《用户协议》</a>
          </li>
          <li>
            <Button @click="login()" type="primary" long>登 录</Button>
          </li>
        </ul>
      </div>
    </div>
</template>

<script>

  import Vue from 'vue';
  import { Button} from 'iview';
  Vue.component('Button',Button);

  // 数据获取；
  import request from '../../../static/js/request'
  Vue.use(request);

  export default {
    name: "Login",
    data(){
      return{
        d:{
          'phone':'',
          'code':'2340'
        },
        isChoose:false,
        tip:'',
        isClickYzm:true,
        time:60
      }
    },
    methods:{
      // 设置cookie ;
      setCookie: function (cname, cvalue, exdays) {
        var d = new Date();
        d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
        var expires = "expires=" + d.toUTCString();
        // console.info(cname + "=" + cvalue + "; " + expires);
        document.cookie = cname + "=" + cvalue + "; " + expires;
        // console.info(document.cookie);
      },

      checkPhone(val){
        if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(val))){
          return false;
        }else {
          return true;
        }
      },
      chooseF(){
        this.isChoose = !this.isChoose;
      },
      close(){
        this.$emit('close','false')
      },
      getNub(){
        var _this = this;
        // console.log(_this.d);
        if( !_this.checkPhone(_this.d.phone) ){
          _this.tip = '请输入正确的手机号'
        }else {
          _this.tip = '';
          _this.isClickYzm = false;

          var timer = setInterval(function () {
            _this.time--;
            if(_this.time<1){
              clearInterval(timer);
              _this.time = 60;
              _this.$set(_this ,'isClickYzm','true');
            };
          },1000)
        }
        // request.getDataPackage('Home/sendSMS',_this.d,function (data) {
        //   console.log(data);
        // });
      },
      login(){
        var _this = this;
        if( !_this.isChoose ){
          _this.tip = '请先同意【用户协议】';
          return false;
        }
        console.log(_this.d);
        request.getDataPackage('Home/login',_this.d,function (data) {
          console.log(data.data);
          if(data.msg == "成功"){
            _this.setCookie('member_alex',data.data.member_alex);
            _this.setCookie('member_header',data.data.member_header);
            _this.setCookie('token',data.data.token);

            _this.$emit('close','true');

          }
        });
        
      }
    }
    ,props:{
      showLogin:''
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../static/scss/public.scss";

  .bg{
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    background:rgba(23,24,27,1);
    opacity:0.7;
    z-index: 901;
  }

  .tip{
    position: absolute;
    left: 10px;
    color: #f00;
    top: 5px;
  }
  .unchoose{
    width: 12px;
    height: 12px;
    display: inline-block;
    background: url("../../assets/images/unchoose.png") no-repeat;
    margin-right: 6px;
    margin-left: 10px;
  }
  .choose{
    background-image: url("../../assets/images/choose.png")
  }
  .login-c{
    padding:0 140px;
    position: fixed;
    z-index: 902;
    width:660px;
    height:400px;
    background:rgba(255,255,255,1);
    border-radius:12px;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);

    .close{
      position: absolute;
      right: 40px;
      top: 40px;

      i{
        color: #666;
        font-size: 20px;
      }

    }
    .logo{
      text-align: center;
      margin-top: 70px;
      margin-bottom: 40px;
    }

    ul{

      li{
        position: relative;

        &:nth-child(1),&:nth-child(2){
          border-bottom: 1px solid #E4E4E4;
        }

        b{
          font-size:19px;
          font-weight:400;
          color:rgba(70,70,70,1);display: inline-block;
          padding:10px 30px 10px 10px;
        }
        .yzm{
          font-size:17px;
          font-weight:400;
          color:rgba(39,135,244,1);
          position: absolute;
          right: 10px;
          top: 50%;transform: translateY(-50%);

          &.unclick{
            color: #999;
            cursor: default;
          }
        }
        span{
          display: inline-block;
          width: 270px;
        }
        input{
          font-size:19px;
          border: none;
          padding:8px 13px;
          width: 100%;
        }
        .xy{
          font-size:13px;
          font-weight:400;
          color:rgba(39,135,244,1);
          display: inline-block;
          margin-top: 30px;
          margin-bottom: 10px;
        }
        button{
          line-height: 40px;
          font-size:21px;
          font-weight:400;
        }
      }
    }
  }
</style>



