<template>
  <div class="activity pt72">

    <div class="pageWidth">

      <Site :site="site"></Site>
      <Title title="活动"></Title>
      <ul class="act">
        <li v-for="a ,index in list.lis" :key="index">
          <ActivityOne :data="a"></ActivityOne>
        </li>
      </ul>
      <Page @on-change="onChange" class="pageNext" :current="this.d.page" :total="this.list.total" :page-size="this.d.limit"  prev-text="上一页" next-text="下一页" />

    </div>
  </div>
</template>

<script>

  import Vue from 'vue';
  import { Page } from 'iview';
  Vue.component('Page',Page);


  import Title from '../block/Title'
  import Site from '../block/Site'
  import ActivityOne from '../activity/ActivityOne'

  // 数据获取；
  import request from '../../../static/js/request'
  Vue.use(request);

  export default {
    name: "Activity",
    data(){
      return{
        list:{
          lis:'',
          total:1
        },
        d:{
          'limit':4,
          'page': 1
        }
        ,site:[
          {
            title:'活动',
            src:''
          }
        ]
      }
    },
    components:{
      Title ,Site ,ActivityOne
    },
    methods:{
      onChange(e){
        var _this = this;
        _this.$set(_this.d , 'page' ,e*1 );
        request.getDataPackage('Activity/getActivityList',_this.d,function (data) {
          _this.$set(_this.list , 'lis' ,data.data.list );
        });
      }
    },
    created(){
      var _this = this;
      request.getDataPackage('Activity/getActivityList',_this.d,function (data) {
        _this.$set(_this.list , 'lis' ,data.data.list );
        if(data.data.count){
          _this.$set(_this.list , 'total' ,data.data.count*1 );
        }
      });
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../static/scss/public.scss";

  .activity{
    background: #f4f4f4;
  }
  .act{
    display: flex;
    flex-wrap: wrap;
    margin-top: 30px;

    li{
      background: #fff;
      width:402px;
      margin-right: 30px;
      margin-bottom: 40px;

      &:nth-child(3n){
        margin-right: 0;
      }
    }
  }

  .pageNext{
    text-align: center;
    padding-bottom: 120px;
    padding-top: 30px;

  }


</style>



