<template>
  <div class="activityOne" @mouseover="showHover" @mouseout="hideHover">
    <a class="img-a"><img :src="data.act_imgs" :alt="data.act_name"></a>
    <p class="p0">{{data.act_name}}</p>

    <a class="overtime">{{data.time}}</a>
    <div class="tag" v-if="data.is_jing == 1">
      <Tag color="gold">推荐</Tag>
    </div>

    <div class="innerhover-border" :class="{showO:isShow}">
      <i class="hover-border-1"></i>
      <div class="innerhover-border-inner">
        <h4 class="p1">{{data.act_name}}</h4>
        <p class="p2">{{data.act_address}}</p>
        <p class="p3">{{price}}</p>
      </div>
    </div>

    <a class="herf-mask" @click="newPage()" :title="data.act_name"></a>

  </div>
</template>

<script>
  import Vue from 'vue';
  import { Tag } from 'iview';
  Vue.component('Tag',Tag);

  export default {
    name: "ActivityOne"
    ,data(){
      return{
        isShow:false
      }
    }
    ,methods:{
      showHover(){
        this.isShow = true;
      },
      hideHover(){
        this.isShow = false;
      }
      , newPage(){
        const old = this.$router.resolve({
          path: 'activityDetail',
          query: {
            id:this.data.id
          }
        });
        window.open(old.href, '_blank')
      }
    }
    , props:{
      data:''
    }
    ,computed:{
      price:function () {

        let str = (this.data.act_price*1 + 0.00);
        if(str == '0'){
          str ='免费'
        }else {
          str = '￥' + this.data.act_price;
        }
        // let str = '￥' + this.data.min_price + 0.00 + '--' + this.data.max_price;
        // if(this.data.max_price==this.data.min_price){
        //   if(this.data.max_price==0){
        //     str = '免费'
        //   }else {
        //     str = this.data.max_price
        //   }
        // }
        return str;
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../static/scss/public.scss";

  .activityOne{
    position: relative;

    &:hover{

      .p0{
        color: $main-color;
      }
      img{
        transform: scale(1.3);
      }
    }
    .img-a{
      display: inline-block;
      overflow: hidden;
      width:402px;
      height:207px;
      position: relative;
    }
    img{
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      transform: scale(1);
      transition: all .3s;
    }
    .p0{
      font-size:15px;
      color:rgba(70,70,70,1);
      line-height:20px;
      height: 60px;
      padding: 10px 10px 10px 10px;
    }
    .overtime{
      font-size:11px;
      color: $main-color;
      padding: 0 0 10px 10px;
      display: inline-block;
    }
    .tag{
      text-align: right;
      float: right;
      padding:0 5px 5px 0;
    }
    .innerhover-border{
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      background: rgba(0,0,0,.6);
      height:207px;
      text-align: center;
      display: flex;
      align-items: center;
      opacity: 0;
    }
    .showO{
      opacity: 1;
    }
    .innerhover-border-inner{
      margin: auto;
    }
    .hover-border-1{
      width:392px;
      height:197px;
      border:1px solid rgba(255,255,255,1);
      opacity:0.4;
      position: absolute;
      left:5px;
      top: 5px;
    }
    .p1{
      font-size:17px;
      color:rgba(238,238,238,1);
      line-height:24px;
      margin-bottom: 20px;
    }
    .p2{
      font-size:13px;
      color:rgba(192,192,192,1);
      line-height:20px;
      margin-bottom: 20px;
    }
    .p3{
      font-size:15px;
      color:rgba(235,164,58,1);
      line-height:20px;
    }
    .herf-mask {
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      font-size: 0;
    }
  }
</style>



