  <div class="myActivity">
    <div class="head">
      <h2>
        我参与的活动
        <span>发布活动</span>
      </h2>
      <div class="tab">
        <a href="" class="active">我发布的</a>
        <a href="">我参与的</a>
      </div>
    </div>
    <ul class="act">
      <li @click="newPage()">
        <div class="activityOne over">
          <a class="img-a"><img src="http://cdn.vr.xiaoqihui.com/2019-01-29/66648.jpg"></a>
          <p class="p0">2019国际教育展国际教育展国际教育展</p>
          <a class="overtime">2019-10-10</a>
          <div class="tag">
            <Tag color="gold">推荐</Tag>
          </div>
          <a class="herf-mask" title="data.act_name"></a>
        </div>
      </li>
      <li>
        <div class="activityOne">
