          <div class="tag">
            <Tag color="gold">推荐</Tag>
          </div>
          <a class="herf-mask" title="data.act_name"></a>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>

  import Vue from 'vue';
  import ActivityOne from '../activity/ActivityOne'

  // 数据获取；
  import request from '../../../static/js/request'
  Vue.use(request);

  export default {
    name: "MyActivity"
    ,data () {
      return {
        list:{
          lis:'',
          total:1
        },
        d:{
          'limit':4,
          'page': 1
        }
      }
    }
    , components:{
      ActivityOne
    }
    ,methods:{
      newPage(){
        this.$router.push({'name':'myActivityCode'})
      }
    },
    created(){
      var _this = this;
      request.getDataPackage('Activity/participateActivity',_this.d,function (data) {
        console.log(data);
        _this.$set(_this.list , 'lis' ,data.data );
        if(data.data.count){
          _this.$set(_this.list , 'total' ,data.data.count*1 );
        }
      });
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../static/scss/public.scss";

  .myActivity{
    background: #fff;
    min-height: 620px;

    .head{
      border-bottom: 1px solid #F4F4F4;
    }
    .tab{
      padding-left: 30px;

      a{
        display: inline-block;
        margin-right: 60px;
        padding-bottom: 14px;
        font-size: 15px;
        color: #676767;
        position: relative;
        
        &.active{
          color: $main-color;

          &:after{
            width:18px;
            height:2px;
            background:rgba(39,135,244,1);
            left: 50%;
            bottom: 0;content: '';
            position: absolute;transform: translateX(-50%);
          }
        }
      }
    }
    h2{
      line-height: 64px;
      font-size: 17px;
      color: #333;
      padding-left: 30px;

      span{
        float: right;
        width:124px;
        line-height:44px;
        text-align: center;
        font-size:15px;
        font-family:MicrosoftYaHei;
        font-weight:400;
        color:rgba(255,255,255,1);
        background: $main-color;
        border: none;
        cursor: pointer;
        margin: 10px 30px 0 0;
      }
    }

    .act{
      display: flex;
      flex-wrap: wrap;
      margin-top: 30px;
      margin-left: 26px;

      li{
        background: #fff;
        width:320px;
        margin-right: 30px;
        margin-bottom: 40px;

        &:nth-child(3n){
          margin-right: 0;
        }
      }
    }

  }


  .activityOne{
    position: relative;
    width:100%;
    height:100%;
    background: #F4F4F4;

    &.over{

      &:after{
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;content: '';
        background: url("../../assets/images/guoqi.png") center no-repeat rgba(23,24,27,.4);
      }
    }

    &:hover{

      .p0{
        color: $main-color;
      }
      img{
        transform: scale(1.3);
      }
    }
    .img-a{
      display: inline-block;
      overflow: hidden;
      width:100%;
      height: 164px;
      position: relative;
    }
    img{
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      transform: scale(1);
      transition: all .3s;
    }
    .p0{
      font-size:15px;
      color:rgba(70,70,70,1);
      line-height:20px;
      padding: 10px 10px 10px 10px;
    }
    .overtime{
      font-size:11px;
      color: $main-color;
      padding: 0 0 10px 10px;
      display: inline-block;
    }
    .tag{
      text-align: right;
      float: right;
      padding:0 5px 5px 0;
    }

    .herf-mask {
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      font-size: 0;
    }
  }

</style>



