<template>
  <div class="myMenu">
    <ul>
      <!--<router-link tag="li" :to="{name:m.link}" v-for="m,index in menu" :class="{active:index==nub}" :key="m.id" @click="change(index)">{{m.name}}</router-link>-->
      <li v-for="m,index in menu" :class="{active:index==nub}" :key="m.id" @click="change(index)">{{m.name}}</li>
    </ul>

  </div>
</template>

<script>
  export default {
    name: "MyMenu"
    ,data(){
      return{
        nub:0,
        menu:[
          {
            name:'账号信息'
            ,link:'accountInformation'
          },
          {
            name:'产品信息'
            ,link:'myProductInfo'
          },
          {
            name:'产品清单'
            ,link:'myProductList'
          },
          {
            name:'案例信息'
            ,link:'caseInfo'
          },
          {
            name:'活动'
            ,link:'myActivity'
          },
          {
            name:'需求'
            ,link:'myNeed'
          }
        ]
      }
    }
    ,methods:{
      change(index){
        // console.log(index);
        this.$set(this,'nub',index);
        this.$router.push({'name':this.menu[index].link})
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../static/scss/public.scss";

  .myMenu{

    ul{
      background: #fff;

      li{
        border-bottom: 1px solid #F4F4F4;
        line-height: 64px;
        padding-left: 30px;
        font-size:16px;
        font-family:MicrosoftYaHei;
        font-weight:400;
        color:rgba(103,103,103,1);
        position: relative;
        cursor: pointer;

        &:hover{
          background: #fcfcfc;
          color: $main-color;
        }

        &.active{
          color: $main-color;

          &:after{
            width:4px;
            height:28px;
            background:rgba(39,135,244,1);
            position: absolute;
            content: '';
            left: 0;
            top: 50%;transform: translateY(-50%);
          }
        }
      }
    }
  }

</style>



