<template>
  <div :class="[is ? 'all tabs' : 'tabs']">
    <a v-for="d,index in data" :key="index" :class="{active:index==now}" @click="tabChange(d.cat_id,index)">{{d.cat_name}}</a>
    <b v-if="data.length>=10">
      <span @click="all()" v-if="!is">全部分类<Icon type="ios-arrow-down" /></span>
      <span class="pr" @click="all()" v-else>收起<Icon type="ios-arrow-up" /></span>
    </b>
  </div>
</template>

<script>
  export default {
    name: "Tab",
    data(){
      return{
        now:0,
        is:false
      }
    },
    props:{
      data:''
    },
    methods:{
      tabChange(id,idx){
        // console.log(id);
        this.$emit('change',id);
        this.$set(this , 'now' ,idx );
      },
      all(){
        if(this.is){
          this.$set(this,'is',false)
        }else {
          this.$set(this,'is',true)
        }
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../static/scss/public.scss";

  .tabs{
    border-bottom: 1px solid #EAEAEA;
    margin-top: 10px;
    position: relative;
    height: 50px;
    overflow: hidden;

    &.all{
      height: inherit;
    }

    a{
      display: inline-block;
      padding: 13px 0;
      margin-right: 60px;
      font-size:15px;
      font-weight:400;
      color:rgba(70,70,70,1);
      position: relative;

      &.active{
        color: $main-color;
        font-weight: bold;

        border-bottom: 2px solid $main-color;
      }
    }

    span{
      position: absolute;
      right: 20px;
      top: 12px;
      display: inline-block;
      line-height: 24px;
      padding:0 9px;
      background: #214EF7;
      color: #fff;
      @include border-radius(4px);
      cursor: pointer;

      .ios-arrow-down{
        margin-left: 5px;
      }
      
      &.pr{
        position: relative;
        top: 0;
      }
    }
  }
</style>



