<template>
  <div class="header">
    <Row class="pageWidth">
      <Col span="18">
        <a class="logo" href="/">
          <img src="../../assets/images/logo.png" alt="">
        </a>
        <Menus></Menus>
      </Col>
      <Col span="6" class="topRight">
        <!--搜索-->
        <div class="search" @click="showSearch">
          <Icon custom="iconfont icon-fangdajing" size="24" />
        </div>
        <div class="searchBar" :class="{show: isShow}">
          <Icon @click="hideSearch" custom="iconfont icon-guanbi" size="24" />
          <Input class="input" v-model="value" clearable placeholder="" />
          <Icon color="#fff" custom="iconfont icon-fangdajing" size="24" />
        </div>

        <!--登录-->
        <div class="login" @click="login()" v-if="!isLogin">
          <Avatar icon="md-person" size="large" />
          登录/注册
        </div>
        <div class="login showUp" v-else >
          <span class="img ivu-avatar ivu-avatar-circle ivu-avatar-large ivu-avatar-icon" :style="{'background-image': 'url('+loginInfo.img+')'}"></span>
          {{loginInfo.name}}

          <div class="my">
            <router-link tag="a" to="/accountInformation">我的</router-link>
            <a @click="out()" href="javascript:;">退出</a>
          </div>
        </div>
      </Col>
    </Row>

    <!--登录-->
    <Login :showLogin="isShowLogin" v-on:close="closeFun"></Login>

  </div>
</template>

<script>
  import Vue from 'vue';
  import { Avatar ,Icon ,Input ,Button} from 'iview';
  Vue.component('Avatar',Avatar);
  Vue.component('Icon',Icon);
  Vue.component('Input',Input);

  import Bus from '../../../static/js/bus'

  import Menus from './Menus'
  import Login from '../block/Login'

  export default {
    name: "Header",
    components:{
      Menus ,Login
    },
    data(){
      return{
        value:'',
        isShow:false  // 是否显示搜索框，默认不显示；
        ,isShowLogin:false  // 是否显示登录界面，默认不显示；
        ,isLogin:false  // 是否已登录，默认未登录；
        ,loginInfo:{
          name:'',
          img:''
        }
        ,pageUrl:''
      }
    },
    mounted: function () {
      var vm = this;
      // 用$on事件来接收参数
      Bus.$on('loginEvent', (data) => {
        // console.log(data);
        this.isShowLogin = data.showLogin;
        if(data.pageUrl){
          vm.pageUrl = data.pageUrl;
        }
        return false;
      })
    },
    methods:{
      out(){
        this.setCookie('token','','-1');
        this.$set(this, 'isLogin',false);
        this.$router.push({'name':'content' })
      },

      // 设置cookie ;
      setCookie: function (cname, cvalue, exdays) {
        var d = new Date();
        d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
        var expires = "expires=" + d.toUTCString();
        // console.info(cname + "=" + cvalue + "; " + expires);
        document.cookie = cname + "=" + cvalue + "; " + expires;
        // console.info(document.cookie);
      },
      //获取cookie
      getCookie: function (cname) {
        var name = cname + "=";
        var ca = document.cookie.split(';');
        // console.log("获取cookie,现在循环")
        for (var i = 0; i < ca.length; i++) {
          var c = ca[i];
          // console.log(c);
          while (c.charAt(0) == ' ') c = c.substring(1);
          if (c.indexOf(name) != -1){
            return c.substring(name.length, c.length);
          }
        }
        return "";
      },
      showSearch(){
        this.isShow = true
      },
      hideSearch(){
        this.isShow = false;
        this.value = '';
      },
      login(){
        this.isShowLogin = true;
      },
      closeFun(child){
        this.isShowLogin = false;
        console.log('close');
        if(child == 'true'){
          // console.log('close,成功');
          this.$set(this,'isLogin',true);
          var _this = this
            ,member_alex = _this.getCookie('member_alex')
            ,member_header = _this.getCookie('member_header');

          this.$set(_this.loginInfo,'name',member_alex);
          this.$set(_this.loginInfo,'img',member_header);

          // 登录成功，如果跳转链接存在，就跳转页面并清空链接；
          if(_this.pageUrl){
            _this.$router.push({'name':_this.pageUrl });
            _this.$set(_this,'pageUrl','');
          }

        }
      }
    },
    created(){
      var _this = this
        ,token = _this.getCookie('token')
        ,member_alex = _this.getCookie('member_alex')
        ,member_header = _this.getCookie('member_header')
      ;
      if(token){
        // 显示登录后的框架；
        _this.$set(this,'isLogin',true);
        //设置头像、名称；
        _this.$set(_this.loginInfo,'name',member_alex);
        _this.$set(_this.loginInfo,'img',member_header);
      }
      console.log('【token】:'+ token);
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../static/scss/public.scss";

  .pageWidth{
    position: relative;
  }
  .header{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    z-index: 99;
    background:rgba(227,226,226,.9);
  }
  .logo{
    margin: 12px 0;
    display: inline-block;
    float: left;

    img{
      float: left;
    }
  }
  .topRight{
    margin-top: 14px;
    text-align: right;
    position: relative;

    span{
      margin-right: 9px;
    }
    .iconfont{
      margin-right: 30px;
    }
  }
  .login{
    display: inline-block;
    vertical-align: middle;
    position: relative;
    padding:0 0 18px 0;
    float: right;

    .img{
      width: 40px;
      height: 40px;
      display: inline-block;
      @include border-radius(50%);
      background-size: cover;
    }

    &:hover{
      color: $main-color;
      cursor: pointer;
    }
    &.showUp:hover{

      .my{
        display: block;
      }
    }
  }

  .my{
    display: none;
    position: absolute;
    right: 0;
    top: 58px;
    background: #fff;
    width: 114px;
    border-radius:0 0 10px 10px;
    overflow: hidden;

    a{
      display: block;
      text-align: center;
      font-size: 20px;
      line-height: 26px;
      padding: 10px 0;

      &:hover{
        background: $main-color;
        color: #fff;
      }
    }

  }

  .icon-people{
    color: white;
  }
  .search{
    display: inline-block;
    padding-top: 10px;

    &:hover{
      color: $main-color;
      cursor: pointer;
    }
  }
  .searchBar{
    position: absolute;
    right: 105px;
    top: -14px;
    background: rgba(0, 0, 0, 0.8);
    padding: 18px 0 22px 20px;
    display: none;
    z-index: 900;
    width: 470px;

    .input{
      margin-right: 22px;
      width: 300px;
    }
    .icon-guanbi{
      color: #fde88b;
      cursor: pointer;
    }
  }

</style>



