<template>
  <div class="needOne" @click="newPage(data.id)">
    <a href="">
      <img :src="data.demand_img[0].img_url" alt="">
      <span v-if="data.demand_img.length>1">+{{data.demand_img.length-1}}</span>
    </a>
    <p>{{data.demand_content}}</p>
    <Avatar :src="data.member_header" />
    <b>{{data.member_alex}}</b>
  </div>
</template>

<script>
  import Vue from 'vue';

  // 数据获取；
  import request from '../../../static/js/request'
  Vue.use(request);

  export default {
    name: "NeedOne",
    props:{
      data:''
    }
    ,methods:{
      newPage(id){
        const old = this.$router.resolve({
          path: 'needDetail?id='+id
        });
        console.log(old);
        window.open(old.href, '_blank')
      }
    }
  }
</script>

<style scoped lang="scss">
  .needOne{
    width:156px;

    a{
      position: relative;
      height:104px;
      overflow: hidden;
      display: block;

      &:hover{

        img{
          transform: scale(1.3);
        }
      }

      img{
        position: absolute;
        width:156px;
        height:104px;
        transform: scale(1.0);
        transition: all .3s;
      }
      span{
        display: inline-block;
        background:rgba(23,24,27,.4);
        color: #fff;
        padding:3px 5px;
        position: absolute;
        right: 0;
        bottom: 0;
      }
    }
    p{
      display: -webkit-box;overflow: hidden;text-overflow: ellipsis;word-wrap: break-word;white-space: normal!important;-webkit-line-clamp: 2;-webkit-box-orient: vertical;
      margin: 16px 8px 24px;
      height: 34px;
    }
    b{
      display: inline-block;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      width: 100px;
      margin: 0 0 -5px 10px;
    }
  }
</style>



