<template>
  <div class="banner" @mouseover="clearTime" @mouseout="startTime">

    <ul>
      <li v-for="img ,index in imgs" :class="{'active':active==index }">
        <a :href="img.link" :style="{ 'background-image': 'url(' + img.ad_img+ ')' }"></a>
      </li>
    </ul>

    <div class="round">
      <span
        :style="{ 'width' : 100/imgs.length + '%'}"
        @mouseover="updateNub(index)"
        :class="{'active':active==index}"
        v-for="(img ,index) in imgs">
          {{img.ad_name}}
      </span>
    </div>

  </div>
</template>

<script>
  export default {
    name: "BannerSmall",
    data(){
      return {
        active:0
      }
    }
    ,props:{
      imgs:''
      ,time:{
        default:null
      }
      ,autoPlay:{
        default:null
      }
    }
    , methods:{
      updateNub(index) {
        this.active = index;
      }
      ,autoPlayBanner(){
        this.auto = setInterval( () => {
          this.updateNub( this.next )
        },this.time)
      }
      ,clearTime(){
        clearInterval(this.auto);
      }
      ,startTime(){
        if(this.autoPlay){
          this.autoPlayBanner();
        }
      }
    },
    computed:{
      next() {
        if (this.active === this.imgs.length-1) {
          return 0
        } else {
          return this.active + 1
        }
      }
    }
    ,created(){
      if(this.autoPlay){
        this.autoPlayBanner();
      }

    }
  }
</script>


<style scoped lang="scss">
  @import "../../../static/scss/public";

  .banner{
    height: 434px;
    position: relative;

    ul{
      position: relative;
    }

    li{
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      height: 434px;
      overflow: hidden;
      display: none;

      &.active{
        display: block;
      }
      a{
        display: inline-block;
        width: 100%;
        height: 100%;
        background-size: cover;
        background-repeat: no-repeat;
        background-position:center 0;
      }
      img{
        position: relative;
        top: 50%;
        -webkit-transform: translateY(-50%);
        -moz-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
        -o-transform: translateY(-50%);
        transform: translateY(-50%);
      }
    }

    .round{
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      text-align: center;
      height: 40px;
      line-height: 38px;
      background:rgba(230,230,230,1);
      color: #464646;


      span{
        display: inline-block;
        color: #464646;
        cursor: pointer;
        text-align: center;
        border-bottom: 2px solid rgba(230,230,230,1);
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        padding: 0 1%;


        &.active{
          background: #F4F4F4;
          color: $main-color;
          border-color:$main-color ;
        }
      }
    }
  }
</style>



