<template>
    <div class="infoList pt72">
      <div class="pageWidth">

        <Site :site="site"></Site>
        <Title title="招标信息" info="行业最新招标信息"></Title>
        <Tab :data="tab" v-on:change="changeFun"></Tab>
        <ul class="com">
          <li v-for="a ,index in list.lis" :key="index">
            <Info :data="a"></Info>
          </li>
        </ul>
        <Page @on-change="onChange" class="pageNext" :current="this.d.page" :total="this.list.total" :page-size="this.d.limit"  prev-text="上一页" next-text="下一页" />

      </div>
    </div>
</template>

<script>

  import Vue from 'vue';
  import { Page } from 'iview';
  Vue.component('Page',Page);

  import Title from '../block/Title'
  import Site from '../block/Site'
  import Info from '../info/Info'
  import Tab from '../block/Tab'

  // 数据获取；
  import request from '../../../static/js/request'
  Vue.use(request);

    export default {
        name: "InfoList",
      data(){
        return{
          url:'Procurement/getProcurementList',
          list:{
            lis:'',
            total:1
          },
          d:{
            'limit':15,
            'page': 1,
            'type':'0'
          },
          tab:''
          ,site:[
            {
              title:'招标信息',
              src:''
            }
          ]
        }
      },
      components:{
        Title ,Site ,Info ,Tab
      },
      methods:{
        onChange(e){
          var _this = this;
          _this.$set(_this.d , 'page' ,e*1 );
          request.getDataPackage(_this.url,_this.d,function (data) {
            // console.log(data);
            _this.$set(_this.list , 'lis' ,data.data.list );
          });
        }
        ,changeFun(child){
          var _this = this;
          _this.d.type = child;
          _this.$set(_this.d , 'page' ,1 );
          // console.log(child ,_this.d);

          request.getDataPackage(_this.url,_this.d,function (data) {
            // console.log(data);
            _this.$set(_this.list , 'lis' ,data.data.list );
            _this.$set(_this.list , 'total' ,data.data.count*1 );
          });

        }
      },
      created(){
        var _this = this;
        request.getDataPackage(_this.url,_this.d,function (data) {
          console.log(data);
          _this.$set(_this.list , 'lis' ,data.data.list );
          if(data.data.count){
            _this.$set(_this.list , 'total' ,data.data.count*1 );
          }
          if(data.data.type){
            _this.$set(_this , 'tab' ,data.data.type );
          }
        });
      }
    }
</script>


<style scoped lang="scss">
  @import "../../../static/scss/public.scss";

  .infoList{
    background: #f4f4f4;
  }
  .com{
    display: flex;
    flex-wrap: wrap;
    margin-top: 30px;
    margin-bottom: 20px;

    li{
      width: 100%;
    }
  }

  .pageNext{
    text-align: center;
    padding-bottom: 120px;
    padding-top: 30px;

  }

</style>



