<template>
  <div class="contents">

    <Banners :data="imgs" :loop="true" :autoPlay="true" :autoPlaySpeed="5000"></Banners>

    <div class="bg1">
      <div class="pageWidth left-right one">

        <div class="indexL">
          <BannerSmall :imgs="bannerSmall" :time=3000 :autoPlay="true"></BannerSmall>

          <Title title="活动" more="更多" link="activity"></Title>
          <ul class="act">
            <li v-for="a ,index in activity" v-if="index<2" :key="index">
              <ActivityOne :data="a"></ActivityOne>
            </li>
          </ul>

        </div>

        <div class="indexR">

          <News :data="news"></News>

          <Title title="需求" more="更多" link="need"></Title>

          <div class="need">
            <NeedOne v-for="n ,index in need" v-if="index<3" :key="index" :data="n"></NeedOne>
          </div>

          <div class="add">
            <a href="javascript:;" @click="publishNeed()"><Icon custom="iconfont icon-bianji" size="20" />发布需求</a>
          </div>

        </div>

      </div>
    </div>

    <div class="bg2">
      <div class="pageWidth ">
        <Title title="产品库" info="行业最新产品资料库" more="更多" link="goods"></Title>
        <div class="goods">
          <GoodsOne v-for="c ,index in goods" v-if="index<8" :key="index" :data="c"></GoodsOne>
        </div>
      </div>
    </div>

    <div class="bg1">
      <div class="pageWidth">
        <Title title="企业库" info="教育行业相关企业" more="更多" link="company"></Title>
        <div class="company">
          <CompanyOne v-for="c ,index in companys" v-if="index<12" :key="index" :data="c"></CompanyOne>
        </div>
      </div>
    </div>

    <div class="bg2">
      <div class="pageWidth ">
        <Title title="招标信息" info="行业最新招标信息" more="更多" link="infoList"></Title>
        <div class="infos">
          <Info v-for="c ,index in info" v-if="index<6" :key="index" :data="c"></Info>
        </div>
      </div>
    </div>

    <!--清单按钮-->
    <InventoryIcon></InventoryIcon>


  </div>
</template>

<script>
  import Vue from 'vue';
  import { Row, Col ,Layout ,Header ,Content ,Sider ,Footer ,Collapse ,Panel ,Tabs ,TabPane ,Icon } from 'iview';
  Vue.component('Row',Row);
  Vue.component('Col',Col);
  Vue.component('Layout',Layout);
  Vue.component('Header',Header);
  Vue.component('Content',Content);
  Vue.component('Sider',Sider);
  Vue.component('Footer',Footer);
  Vue.component('Collapse',Collapse);
  Vue.component('Panel',Panel);
  Vue.component('Tabs',Tabs);
  Vue.component('TabPane',TabPane);
  Vue.component('Icon',Icon);


  import Banners from './Banners'
  import Title from '../block/Title'
  import BannerSmall from './BannerSmall'
  import ActivityOne from '../activity/ActivityOne'
  import News from '../new/News'
  import NeedOne from '../need/NeedOne'
  import GoodsOne from '../goods/GoodsOne'
  import CompanyOne from '../company/CompanyOne'
  import Info from '../info/Info'
  import InventoryIcon from '../block/InventoryIcon'
  import Login from '../block/Login'

  // 数据获取；
  import Bus from '../../../static/js/bus'
  import request from '../../../static/js/request'
  Vue.use(request);

  export default {
    name: "Contents",
    data () {
      return {
        value1: '2',
        imgs:'',
        bannerSmall:'',
        activity:'',
        news:[
          {
            title:'推荐',
            content:''
          },
          {
            title:'产品',
            content:''
          },
          {
            title:'企业',
            content:''
          },
          {
            title:'招标',
            content:''
          },
          {
            title:'需求',
            content:''
          },
          {
            title:'活动',
            content:''
          }
        ],
        need:'',
        goods:'',
        companys:'',
        info:'',
        allData:''
        ,isShowLogin:false  // 是否显示登录界面，默认不显示；
        ,loginEvent:{
          showLogin:false,
          pageUrl:''
        }
      }
    },
    methods:{
      // 发布需求；
      publishNeed(){
        if(this.getCookie('token')){
          this.$router.push({'name':'myNeed'})
        }else {
          console.log('login');
          this.$set(this.loginEvent, 'showLogin' ,true);
          this.$set(this.loginEvent, 'pageUrl' ,'myNeed');

          Bus.$emit('loginEvent', this.loginEvent);
        }
      }
      ,getCookie(cname) {
        var name = cname + "=";
        var ca = document.cookie.split(';');
        // console.log("获取cookie,现在循环")
        for (var i = 0; i < ca.length; i++) {
          var c = ca[i];
          while (c.charAt(0) == ' ') c = c.substring(1);
          if (c.indexOf(name) != -1){
            return c.substring(name.length, c.length);
          }
        }
        return "";
      }
    },
    components:{
      Banners ,Title ,BannerSmall ,ActivityOne ,News ,NeedOne ,GoodsOne ,InventoryIcon ,CompanyOne ,Info ,Login
    },
    created(){
      var _this = this;
      request.getDataPackage('Home/index',{},function (data) {

        console.log(data.data);

        _this.$set(_this, 'imgs' ,data.data.banner_ad);

        _this.allData = data.data;
        _this.imgs = data.data.banner_ad;
        _this.bannerSmall = data.data.recommend_ad;
        _this.activity = data.data.act_ad;
        _this.need = data.data.need_ad;
        _this.goods = data.data.goods_ad;
        _this.companys = data.data.qy_ad;
        _this.info = data.data.procurement_ad;
        _this.news[0].content = data.data.new_tui;
        _this.news[1].content = data.data.new_goods;
        _this.news[2].content = data.data.new_company;
        _this.news[3].content = data.data.new_procurement;
        _this.news[4].content = data.data.new_need;
        _this.news[5].content = data.data.new_activity;
        console.log(data)
      });
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../static/scss/public.scss";
  .left-right{
    display: flex;
    justify-content: space-between;
  }
  .title{
    margin-bottom: 30px;
  }
  .pageWidth{
    padding: 90px 0;

  }
  .one{
    padding-top: 40px;
  }
  .indexL .banner{
    margin-bottom: 60px;
  }
  .act{
    display: flex;
    justify-content: space-between;

    li{
      background: #fff;
      width:402px;
    }
  }



  .need{
    display: flex;
    justify-content: space-between;
  }

  .add{
    border:1px solid rgba(39,135,244,1);
    font-size:15px;
    color:rgba(255,255,255,1);
    margin-top: 40px;

    a{
      border: 3px solid #fff;
      background: #2787F4;
      color: #fff;
      display: block;
      line-height: 44px;
      text-align: center;

      .iconfont{
        margin-right: 10px;
      }
    }
  }

  .company{
    display: flex;
    flex-wrap: wrap;


    .companyOne{
      margin-bottom: 36px;
      margin-right: 26px;

      &:nth-child(6n){
        margin-right: 0;
      }
    }
  }
  .goods{
    display: flex;
    flex-wrap: wrap;


    .goodsOne{
      margin-bottom: 20px;
      margin-right: 16px;

      &:nth-child(4n){
        margin-right: 0;
      }
    }
  }

</style>



