<template>

  <div class="news">

    <!--标题-->
    <div class="tab">
      <a href="javascript:;" @mouseover="now=index" v-for="n,index in data" :class="{active: index == now }">{{n.title}}</a>
    </div>

    <!--内容-->
    <div class="new-tab-content">

      <ul class="new-tab-content-ul" v-if="now == 0">
        <li :class="{ 'first':index==0 ,'newsitem':index!=0 ,'inform':n.type == '企业' ,'event':n.type == '需求' ,'amusement':n.type == '活动' ,'tutorial':n.type == '产品'}"
            v-if="index<8"
            v-for="n,index in data[0].content" :key="index"
            @click="detailAll(n.type ,n)">
          <span class="item-type" v-if="index != 0">{{n.type}}</span>
          <a class="item-href" target="_blank" href="" >
            <Icon v-if="index==0" custom="iconfont icon-jiantou_xiangyou" size="40" />
            {{n.title}}
          </a>
          <span class="item-time" v-if="index != 0">{{timeStamp(n.ct_time)}}</span>
        </li>
      </ul>

      <ul class="new-tab-content-ul" v-if="now == 1">
        <li :class="{ 'first':index==0 ,'newsitem':index!=0 ,'tutorial':true}"
            v-if="index<8"
            v-for="n,index in data[1].content" :key="index"
            @click="detail('goodDetail' ,n.goods_id)">
          <span class="item-type" v-if="index != 0">产品</span>
          <a class="item-href" target="_blank" href="" >
            <Icon v-if="index==0" custom="iconfont icon-jiantou_xiangyou" size="40" />
            {{n.goods_name}}
          </a>
          <span class="item-time" v-if="index != 0">{{timeStamp(n.ct_time)}}</span>
        </li>
      </ul>

      <ul class="new-tab-content-ul" v-if="now == 2">
        <li :class="{ 'first':index==0 ,'newsitem':index!=0 }"
            v-if="index<8"
            v-for="n,index in data[2].content" :key="index"
            @click="detail('companyDetail' ,n.company_id)">
          <span class="item-type" v-if="index != 0">企业</span>
          <a class="item-href" target="_blank" href="" >
            <Icon v-if="index==0" custom="iconfont icon-jiantou_xiangyou" size="40" />
            {{n.com_name}}
          </a>
          <span class="item-time" v-if="index != 0">{{timeStamp(n.ct_time)}}</span>
        </li>
      </ul>

      <ul class="new-tab-content-ul" v-if="now == 3">
        <li :class="{ 'first':index==0 ,'newsitem':index!=0 ,'inform':true}"
            v-if="index<8"
            v-for="n,index in data[3].content" :key="index"
            @click="detail('infoDetail' ,n.id)">
          <span class="item-type" v-if="index != 0">招标</span>
          <a class="item-href" target="_blank" href="" >
            <Icon v-if="index==0" custom="iconfont icon-jiantou_xiangyou" size="40" />
            {{n.title}}
          </a>
          <span class="item-time" v-if="index != 0">{{timeStamp(n.ct_time)}}</span>
        </li>
      </ul>

      <ul class="new-tab-content-ul" v-if="now == 4">
        <li :class="{ 'first':index==0 ,'newsitem':index!=0 ,'event':true}"
            v-if="index<8"
            v-for="n,index in data[4].content" :key="index"
            @click="detail('needDetail' ,n.id)">
          <span class="item-type" v-if="index != 0">需求</span>
          <a class="item-href" target="_blank" href="" >
            <Icon v-if="index==0" custom="iconfont icon-jiantou_xiangyou" size="40" />
            {{n.demand_content}}
          </a>
          <span class="item-time" v-if="index != 0">{{timeStamp(n.ct_time)}}</span>
        </li>
      </ul>

      <ul class="new-tab-content-ul" v-if="now == 5">
        <li :class="{ 'first':index==0 ,'newsitem':index!=0 ,'amusement':true}"
            v-if="index<8"
            v-for="n,index in data[5].content" :key="index"
            @click="detail('activityDetail' ,n.id)">
          <span class="item-type" v-if="index != 0">活动</span>
          <a class="item-href" target="_blank" href="" >
            <Icon v-if="index==0" custom="iconfont icon-jiantou_xiangyou" size="40" />
            {{n.act_name}}
          </a>
          <span class="item-time" v-if="index != 0">{{timeStamp(n.ct_time)}}</span>
        </li>
      </ul>

      <a @click="newPage('/newList')" class="btn-morenews">
        <span class="c1">阅读更多</span><Icon custom="iconfont icon-jiantou_xiangyou" size="20" />
      </a>

    </div>

  </div>

</template>

<script>
  import Vue from 'vue';
  import { Icon } from 'iview';

  Vue.component('Icon',Icon);
  export default {
    name: "News",
    data(){
      return{
        now:'0'
      }
    },
    props:{
      data:''
    }
    ,methods:{
      detailAll(type ,n){
        var link = '' ,ids='';
        switch (type) {
          case '产品':
            link = 'goodDetail';
            ids = n.goods_id;
            break;
          case '企业':
            link = 'companyDetail';
            ids = n.id;
            break;
          case '招标':
            link = 'infoDetail';
            ids = n.id;
            break;
          case '需求':
            link = 'needDetail';
            ids = n.id;
            break;
          case '活动':
            link = 'activityDetail';
            ids = n.id;
            break;
        }
        const old = this.$router.resolve({
          path: link,
          query: {
            id:ids
          }
        });
        window.open(old.href, '_blank')
      },
      detail(link,id){
        const old = this.$router.resolve({
          path: link,
          query: {
            id:id
          }
        });
        window.open(old.href, '_blank')
      },
      addZero(nub) {
        return nub > 9 ? nub : '0' + nub;
      },
      timeStamp(d){
        return this.addZero( new Date(d*1000).getMonth()+1 ) + '-' + this.addZero( new Date(d*1000).getDate() );
      }
      ,
      newPage(link){
        const old = this.$router.resolve({
          path: link
        });
        console.log(old);
        window.open(old.href, '_blank')
      }
    }
    ,computed:{
      cla(i){
        return {
          newsitem: 1
        }
      }
    }
  }
</script>

<style scoped lang="scss">

  @import "../../../static/scss/public.scss";
  .news{
    margin-bottom: 60px;
    min-height: 432px;

    .tab{
      display: flex;
      justify-content: space-between;

      a{
        font-size:17px;
        color:rgba(103,103,103,1);

        &.active{
          color: $main-color;
          font-weight: bold;
          position: relative;

          &:after{
            position: absolute;
            content: '';
            border-bottom: 2px solid $main-color;
            bottom: -8px;
            left: 0;
            right: 0;
          }
        }
      }
    }
  }

  .btn-morenews{display:block;width:496px;height:40px;background-color:#e3e2e2;line-height:40px;text-align:center;
    margin-top: 10px;}
  .btn-morenews:hover{background-color:#dad8d8;border-radius:4px}
  .btn-morenews:hover>i{animation-name:arrowanim;-webkit-animation-name:arrowanim}
  .btn-morenews{

    .c1{font-size:14px;
      color:$main-color;
    }
    .ivu-icon{
      margin-top: -3px;
    }
  }
  .btn-morenews>.c2{font-size:14px;color:#7ea1a6}


  .new-tab-content-ul{position:relative;
    margin-top: 11px;}
  .new-tab-content-ul>.first{
    margin-bottom: 7px;list-style:none;background-position:0 -458px;width:496px;height:59px;overflow:hidden;line-height:59px;text-align:center;font-size:22px;font-weight:700;box-sizing:border-box;padding:0 12px;text-overflow:ellipsis;white-space:nowrap;color:#0da0b4}
  .new-tab-content-ul>.first>.item-href{color:#0da0b4}
  .new-tab-content-ul>.first>.item-href:hover{color:#bb9a6c}

  .news-type-list{width:940px;height:996px;box-sizing:border-box;overflow:hidden;padding:20px 40px 0 40px;position:relative}
  .news-type-list>.newsitem{width:100%;height:61px;line-height:60px;overflow:hidden;border-top:none;border-bottom:1px #eff1f1 solid}
  .news-type-list>.newsitem>.item-href{width:660px;height:60px}
  .news-type-list>.newsitem>.item-time{width:120px;height:60px;font-size:14px;letter-spacing:1px;margin-left:20px}

  .newsitem{list-style:none;width:496px;height:40px;line-height:39px;overflow:hidden;box-sizing:border-box;border-top:1px #e0e2e2 solid;text-align:left;font-size:0}
  .newsitem>.item-type{display:inline-block;vertical-align:middle;*zoom:1;*display:inline;width:36px;height:20px;box-sizing:border-box;font-size:12px;border:1px #69c5d2 solid;color:#1da6ba;line-height:18px;text-align:center;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;}
  .newsitem>.item-href{display:inline-block;vertical-align:middle;*zoom:1;*display:inline;width:300px;height:39px;overflow:hidden;margin-left:20px;color:#424242;font-size:14px;letter-spacing:1px;text-overflow:ellipsis;white-space:nowrap}
  .newsitem>.item-href:hover{color:#1da6ba;text-decoration:none}
  .newsitem>.item-time{display:inline-block;vertical-align:middle;*zoom:1;*display:inline;width:120px;height:39px;overflow:hidden;text-align:right;font-size:14px;letter-spacing:1px;color:#9d9d9d;margin-left:18px}
  .newsitem.inform>.item-type{color:#bb9a6c;border:1px #c5ab86 solid;}
  .newsitem.inform>.item-href:hover{color:#bb9a6c}
  .newsitem.event>.item-type{color:#6388c5;border:1px #97afc5 solid;}
  .newsitem.event>.item-href:hover{color:#6388c5}
  .newsitem.amusement>.item-type{color:#e9852d;border:1px #e9852d solid;}
  .newsitem.amusement>.item-href:hover{color:#e9852d}
  .newsitem.tutorial>.item-type{color:#4ba36a;border:1px #4ba36a solid}
  .newsitem.tutorial>.item-href:hover{color:#4ba36a}
</style>



