<template>
  <div class="inventoryIcon">
    <h3>制作清单</h3>
    <div class="c">
      <span><i class="iconfont icon-qingdan"></i></span>
      <p>添加产品至清单</p>
    </div>
  </div>
</template>

<script>
    export default {
        name: "InventoryIcon"
    }
</script>

<style scoped lang="scss">
  @import "../../../static/scss/public.scss";

  .inventoryIcon{
    font-family:MicrosoftYaHei;
    font-weight:400;
    background: #fff;
    @include border-radius(10px);
    overflow: hidden;
    text-align: center;
    min-width: 150px;
    padding-bottom: 23px;
    position: fixed;
    right: 20px;
    top: 40%;

    h3{
      font-size:17px;
      font-weight:bold;
      line-height: 40px;
      color:rgba(255,255,255,1);
      background: #2787F4;
    }
    .c{
      padding-top: 20px;
      cursor: pointer;

      span{
        width: 40px;
        height: 43px;
        display: inline-block;
        background: #2787F4;
        @include border-radius(7px);
        position: relative;
      }

      i{
        font-size: 40px;
        color: #fff;
        margin-top: -10px;
        position: absolute;
        left: 0;
      }

      p{
        margin-top: 6px;
        font-size:13px;
        color:rgba(143,193,251,1);
      }
    }
  }
</style>



