<template>
  <div class="goodDetail pt72">
    <div class="pageWidth">

      <Site :site="site"></Site>
      <Title title="产品详情"></Title>

      <div class="goodDetail-head">
        <ul>
          <li>
            <div class="imgs">
              <Carousel radius-dot :autoplay-speed="3000" autoplay v-model="value2" loop>
                <CarouselItem v-for="img ,index in data.image" :key="index">
                  <div class="demo-carousel"><img :src="img" alt=""></div>
                </CarouselItem>
              </Carousel>
            </div>
            <h3>{{data.goods_name}}</h3>
            <div class="tag">
              <Tag v-for="t,index in data.goods_tag" :key="index" color="gold">{{t}}</Tag>
            </div>
            <p>产品分类：智慧校园</p>
            <p>适用对象：大学，中学，中学，小学</p>
            <a class="plus" href=""><Icon type="md-add" />添加到产品清单</a>
            <div class="company">
              上海倍他漫想文化传播有限公司 <a href="">查看企业</a>
            </div>
          </li>
        </ul>
      </div>

      <div class="mainAbout">
        <div v-for="g ,index in data.goods_info" :key="index">
          <p class="img" v-if="g.type == 2"><img :src="g.content" alt=""></p>
          <p v-else>{{g.content}}</p>
        </div>
        <a class="more" href="">查看更多资料</a>
      </div>
      <div class="detailInfo">
        {{data.com_info}}
      </div>

      <Title title="产品案例" v-if="goodList.length"></Title>

      <div class="goodList" v-if="goodList.length">
        <ul>
          <GoodsOneLang v-for="g ,index in showNub" :key="index" v-if="goodList[index]" :data="goodList[index]"></GoodsOneLang>
        </ul>
        <div class="loadMore" @click="loadMore()" v-if="showMore">点击加载更多</div>
      </div>

    </div>
  </div>
</template>


<script>


  import Vue from 'vue';
  import { Tag ,Carousel ,CarouselItem ,Icon } from 'iview';
  Vue.component('Icon',Icon);
  Vue.component('Carousel',Carousel);
  Vue.component('CarouselItem',CarouselItem);
  Vue.component('Tag',Tag);

  import Title from '../block/Title'
  import Site from '../block/Site'
  import GoodsOneLang from '../../components/goods/GoodsOneLang'

  // 数据获取；
  import request from '../../../static/js/request'
  Vue.use(request);

  export default {
    name: "GoodDetail",
    components:{
      Title ,Site ,GoodsOneLang
    },
    data(){
      return{
        value2:0,
        data:'',
        tel:'',
        d:{
          'goods_id':this.$route.query.id
        }
        ,site:[
          {
            title:'产品列表',
            src:'/goods'
          },
          {
            title:'产品详情',
            src:''
          }
        ]
        ,goodList:''
        ,showNub:5
        ,showMore:false
      }
    },
    methods:{
      loadMore(){
        var _this = this;
        _this.$set(_this , 'showNub' ,_this.goodList.length );
        _this.$set(_this , 'showMore' ,false );
      }
    },
    created(){
      var _this = this;
      console.log(_this.d);
      request.getDataPackage('Goods/getGoodsInfo',_this.d,function (data) {
        console.log(data.data );
        _this.$set(_this , 'data' ,data.data );
        _this.$set(_this , 'goodList' ,data.data.case_list );
        if(_this.goodList.length > 4){
          _this.$set(_this , 'showMore' ,true );
        }
      });
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../static/scss/public.scss";

  .loadMore{
    width:830px;
    line-height:50px;
    background:rgba(244,244,244,1);
    font-size:17px;
    font-weight:400;
    color:rgba(53,163,247,1);
    margin:50px auto 0;
    text-align: center;
    cursor: pointer;
  }

  .goodDetail-head{
    margin-top: 20px;
    margin-bottom: 30px;

    ul{

      li{
        min-height: 220px;
        padding: 0 20px 20px 460px;
        position: relative;

        .imgs{
          width:420px;
          height:280px;
          position: absolute;
          left:0;
          top:0;
          overflow: hidden;

          img{
            max-width: 100%;
          }
        }
        .ivu-carousel{
          height: 100%;
        }
        h3{
          font-size:19px;
          font-weight:bold;
          line-height:24px;
          padding: 15px 0 15px;
        }
        .tag{
          margin-bottom: 15px;

          .ivu-tag{
            margin-right: 10px;
          }
        }
        p{
          font-size: 15px;
          margin-bottom: 10px;
        }
        .company{
          font-size:17px;
          font-weight:400;
          color:rgba(70,70,70,1);
          margin-top: 50px;

          a{
            display: inline-block;
            margin-left: 34px;
          }
        }
        .plus{
          line-height: 30px;
          background: #214EF7;
          color: #fff;
          display: inline-block;
          padding: 0 7px;
          font-size: 15px;
          margin-top: 15px;

          i{
            font-size: 26px;
            margin-right: 10px;
          }
        }

      }
    }
  }

  .mainAbout{
    margin: 30px 20px;
    position: relative;
    font-size:17px;
    color:rgba(70,70,70,1);
    line-height:30px;

    p{
      text-indent: 2em;
      margin: 20px 0;
    }
    .img{
      margin:0;
      text-align: center;
      text-indent: 0;

      img{
        width: 50%;
        display: block;
        margin:  auto;
      }
    }
    .more{
      display: inline-block;
      line-height: 40px;
      padding:0 27px;
      color: #fff;
      font-size:17px;
      font-weight:400;
      background: $main-color;
      margin-top: 10px;
    }

  }
  .detailInfo{
    font-size:17px;
    font-weight:400;
    color:rgba(70,70,70,1);
    line-height:24px;
    margin: 40px;
    white-space: pre-wrap;
  }

  .goodList{
    margin-top: 20px;
    margin-bottom: 200px;

    ul{
      overflow: hidden;

    }
  }

</style>




