<template>
  <div class="menus">
    <Menu mode="horizontal" :theme="theme1" active-name="1">
      <MenuItem name="1">
        <router-link to="/">首页</router-link>
      </MenuItem>
      <!--<MenuItem name="2">-->
        <!--<router-link to="/about">关于我们</router-link>-->
      <!--</MenuItem>-->
      <MenuItem name="2">
        <router-link to="/contact">联系我们</router-link>
      </MenuItem>
      <MenuItem name="3" class="app-down">
        <router-link to="/down">App下载</router-link>
        <p>
          <img src="../../assets/images/ewm.png" alt="">
          <span>扫描二维码下载校企荟</span>
        </p>
      </MenuItem>


    </Menu>
  </div>
</template>

<script>
  import Vue from 'vue';
  import { Menu ,Icon ,Submenu  ,MenuGroup ,MenuItem } from 'iview';
  Vue.component('Menu',Menu);
  Vue.component('Icon' ,Icon);
  Vue.component('Submenu' ,Submenu);
  Vue.component('MenuGroup' ,MenuGroup);
  Vue.component('MenuItem' ,MenuItem);

  export default {
    name: "Menus",
    data(){
      return{
        theme1: 'light'
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../static/scss/public";
  .menus{
    margin-left: 105px;
    display: inline-block;
    float: left;

    a{
      display: block;
      color: #515a6e;

    }
    .ivu-menu-item-selected a ,a:hover{
      color: $main-color;
    }
  }
  .ivu-menu-horizontal{
    float: left;
    height: inherit;
    line-height: 70px;
  }
  .ivu-menu-horizontal .ivu-menu-item, .ivu-menu-horizontal .ivu-menu-submenu{
    padding: 0;
    margin: 0 105px 0 0;
  }

  .ivu-menu-light.ivu-menu-horizontal .app-down.ivu-menu-item{

    border-bottom-color:transparent ;
  }
  .app-down{
    position: relative;
    display: inline-block;

    &:hover{
      color: $main-color;

      p{
        display: block;
      }
    }

    p{
      position: absolute;
      top: 72px;
      background: rgba(0, 0, 0, 0.8);
      text-align: center;
      width: 162px;
      left: 50%;
      transform: translateX(-50%);
      color: #fff;
      padding: 20px 0;
      display: none;

      &:after{
        position: absolute;
        content: '';
        border-bottom: 10px solid rgba(0, 0, 0, 0.8);
        border-left: 10px solid transparent;
        border-right: 10px solid transparent;
        top: -10px;
        left: 50%;
        transform: translateX(-50%);
      }

      img{
        width: 108px;
        height: 108px;
      }
      span{
        display: block;
        margin-right: 0;
        line-height: 0;
        margin-top: -10px;
      }
    }
  }

</style>



