<template>
  <Breadcrumb>
    <BreadcrumbItem to="/">
      <Icon type="ios-home-outline"></Icon> 首页
    </BreadcrumbItem>
    <BreadcrumbItem v-for="s,index in site" :key="index" :to="s.src">
      {{s.title}}
    </BreadcrumbItem>
  </Breadcrumb>
</template>

<script>
  import Vue from 'vue';
  import { Breadcrumb ,BreadcrumbItem ,Icon } from 'iview';
  Vue.component('Breadcrumb',Breadcrumb);
  Vue.component('BreadcrumbItem',BreadcrumbItem);
  Vue.component('Icon',Icon);

  export default {
    name: "Site",
    props:{
      title:'',
      site:''
    }
  }
</script>

<style scoped lang="scss">
  .ivu-breadcrumb{
    padding: 50px 0;
  }
</style>



