<template>
    <div class="activityPay pt72">
      <div class="pageWidth">
        <h2>活动付款</h2>
        <ul>
          <li>
            <span>门票</span>
            <span>参加人数</span>
            <span>总额</span>
          </li>
          <li>
            <span>{{data.title}} ￥{{data.price}}</span>
            <span>{{data.number}}</span>
            <span>￥{{total}}</span>
          </li>
        </ul>
        <div v-if="total">
          <p class="subTit">选择支付方式</p>
          <div class="choose">
            <a href="javascript:;" :class="{active:type==2}" @click="wx()">微信支付<Icon type="md-checkmark" /></a>
            <a href="javascript:;" :class="{active:type==1}" @click="zfb()">支付宝<Icon type="md-checkmark" /></a>
          </div>
          <p class="btn">
            <button class="gray" v-if="type == '-1'">支 付 ￥{{total}}</button>
            <button @click="pay()" v-else>支 付 ￥{{total}}</button>
          </p>
        </div>
        <p class="btn" v-else>
          <button @click="pay('zero')">支 付 ￥{{total}}</button>
        </p>


        <div class="pop" :class="{hide:!is}">
          <div class="popC">
            <Icon @click="close()" type="md-close" size="30" />
            <p class="b" v-if="type == '1'">请用支付宝扫描下方二维码完成付款</p>
            <p class="b" v-if="type == '2'">请用微信扫描下方二维码完成付款</p>
            <div id="qrcode"></div>
            <p class="btn_pop"><button>{{msg}}</button></p>
            <p class="tip">可在<a>我的-活动-我参加的活动</a>中查看凭证码</p>
            <a @click="close()" class="otherPay">选择其他支付方式</a>
          </div>
        </div>

      </div>
    </div>
</template>

<script>

  import Vue from 'vue';
  import { Icon } from 'iview';
  Vue.component('Icon',Icon);

  // 数据获取；
  import request from '../../../static/js/request'
  Vue.use(request);

  import QRCode from 'qrcodejs2'

  // Activity/addOrder  下单
  // id	活动id
  // buy_num	购买数量
  // pay_type	付款类型 0免费 1支付宝 2微信
  // pay_price	实际支付金额
  // actual_desc	实际支付金额的说明
  // original_price	原金额

  export default {
    name: "ActivityPay"
    ,data(){
      return{
        data:this.$route.params.id,
        type:-1,
        is:false,
        orderN:'',
        msg:'等待支付',
        timer:null
      }
    }
    ,computed:{
      total(){
        return this.data.price * this.data.number
      }
    }
    ,methods:{
      query (el) {
        if (typeof el === 'string') {
          var selector = el;
          el = document.querySelector(el);
          if (!el) {
            "development" !== 'production' && warn(
              'Cannot find element: ' + selector
            );
            return document.createElement('div')
          }
        }
        return el
      },
      close(){
        this.$set(this,'is',false);
        this.query('#qrcode').innerHTML = '';
        console.log('close');
        clearInterval(this.timer);
      },
      wx(){
        this.type = 2;
      },
      zfb(){
        this.type = 1;
      },
      cx(nub){
        let d={
          order_no:nub,
          pay_type:this.type
        };
        let _this = this;
        request.getDataPackage('Activity/orderQuery',d,function (data) {
            console.log(data);
            if(data.code==1){
              _this.$set(_this,'msg','支付成功');
              clearInterval(_this.timer);
              setTimeout(function () {
                alert('页面跳转')
              },1000)
            }
        });
      },
      getCookie(cname) {
        var name = cname + "=";
        var ca = document.cookie.split(';');
        // console.log("获取cookie,现在循环")
        for (var i = 0; i < ca.length; i++) {
          var c = ca[i];
          // console.log(c);
          while (c.charAt(0) == ' ') c = c.substring(1);
          if (c.indexOf(name) != -1){
            return c.substring(name.length, c.length);
          }
        }
        return "";
      },
      pay(zero){
        let _this = this ,code
        ,d={
          id:_this.data.id,
          buy_num:_this.data.number,
          pay_type:_this.type,
          pay_price:_this.data.number*_this.data.price,
          actual_desc:_this.data.title,
          original_price:_this.data.price
        };

        //判断是否登录；
        if(!_this.getCookie('token')){
          alert('请先登录再支付');
          return false;
        }


        _this.query('#qrcode').innerHTML = '';
        console.log(d);
        if(_this.total==0){
          d.pay_type=0
        }
        if(d.type=='-1'){
          return false
        }

        request.getDataPackage('Activity/addOrder',d,function (data) {
          console.log(data);

          if(data.code ==1){
            if(zero){
              alert('页面跳转')
            }else {
              // 支付宝
              if(d.pay_type == 1){
                code = data.data.pay.qr_code
              }
              // 微信
              if(d.pay_type == 2){
                code = data.data.pay.code_url
              }
              // 生成二维码
              let qrcode = new QRCode('qrcode', {
                width: 260,
                height: 260, // 高度
                text: code, // 二维码内容
                colorDark: '#000000',
                colorLight: '#ffffff',
                correctLevel: QRCode.CorrectLevel.H
              });

              _this.is = true;

              _this.timer = setInterval(function () {
                _this.cx(data.data.order_no);
              },3000);
            }
          }else {
            alert(data.msg)
          }


        });
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../static/scss/public.scss";

  .activityPay{
    background: #f4f4f4;
    font-family:MicrosoftYaHei;
    font-size:17px;
    padding-bottom: 70px;

    .pageWidth{
      background: #fff;
      margin-top: 50px;
      padding-bottom: 150px;
    }

    h2{
      border-bottom: 1px solid #F4F4F4;
      font-weight:bold;
      color:rgba(51,51,51,1);
      line-height:64px;
      padding-left: 30px;

    }
    ul{
      margin: 0 230px 0 30px;

      li{

        &:nth-child(1){
          border-bottom: 2px solid #9D9D9D;
          font-weight:bold;
          color:rgba(103,103,103,1);
          line-height: 54px;
        }

        &:nth-child(2){
          font-size:21px;
          font-weight:400;
          color:rgba(34,34,34,1);
          padding-top: 20px;

          span:nth-child(3){
            color: $main-color;
          }
        }

        span{
          width: 30%;
          display: inline-block;
          text-align: center;

        }
      }
    }
    .subTit{
      color: #222;
      padding: 86px 0 30px 30px;
    }
    .choose{

      a{
        margin:0 10px 0 30px;
        display: inline-block;
        width: 240px;
        line-height: 72px;
        padding-left: 92px;
        border:1px solid rgba(157,157,157,1);
        color: #9D9D9D;
        background: url("../../../src/assets/images/wx.png") no-repeat 30px 22px;

        &:nth-child(2){
          background-image:url("../../../src/assets/images/zfb.png") ;
        }
        i{
          display: none;
          position: absolute;
        }
        &.active{
          border-color: $main-color;
          position: relative;
          color: $main-color;

          i{
            display: block;
            right: -1px;
            bottom: -1px;
            color: #fff;z-index: 1;
            font-size: 10px;
          }

          &:after{
            position: absolute;
            content: '';
            border-top: 18px solid transparent;
            border-left: 18px solid transparent;
            border-bottom: 18px solid $main-color;
            right: 0;
            bottom: 0;
          }
        }
      }
    }
    .btn{
      margin-top: 80px;
      margin-left: 30px;

      button{
        width:400px;
        line-height:60px;
        text-align: center;
        border: none;
        font-size:21px;
        background:rgba(39,135,244,1);
        color: #fff;
        cursor: pointer;

        &.gray{
          background: #999;
          cursor: default;
        }
      }
    }
  }

  .pop{
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    background:rgba(23,24,27,.7);
    z-index: 911;

    .ivu-icon{
      position: absolute;
      right: 40px;
      top: 40px;cursor: pointer;

    }
    .popC{
      width:950px;
      height:600px;
      background:rgba(255,255,255,1);
      @include border-radius(12px);
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);
      text-align: center;

      .b{
        font-size:23px;
        font-weight:400;
        color:rgba(70,70,70,1);
        margin: 70px 0 30px;
      }
      .btn_pop{

        button{
          width:350px;
          line-height:60px;
          background:rgba(1,127,253,1);
          font-size: 27px;
          border: none;
          color: #fff;
        }
      }
      #qrcode{
        width: 260px;
        height: 260px;
        margin:0 auto 35px;
        position: relative;

        &:after{
          position: absolute;
          width: 100px;
          height: 100px;
          content: '';
          background: url("../../../src/assets/images/logo-new.png");
          background-size: cover;
          left: 50%;
          top: 50%;
          margin: -50px 0 0 -50px;
          border: 4px solid #fff;
          @include border-radius(20px);
        }
      }
      .tip{
        font-size: 13px;
        margin-bottom: 30px;
        margin-top: 8px;
      }
      .otherPay{
        font-size: 23px;
        color: #676767;
      }
    }
  }

</style>



