<template>
    <div class="info" @click="newPage()">
      <h3>
        <a>{{data.title}}</a>
        <span>{{data.time}}</span>
      </h3>
      <div class="infoBar">
        <Tag color="green">{{data.type}}</Tag>
        <span>地域：{{data.area}}</span>
        <span>采购人：{{data.buy_person}}</span>
      </div>
    </div>
</template>

<script>
  import Vue from 'vue';
  import { Tag ,Icon } from 'iview';
  Vue.component('Tag',Tag);

    export default {
      name: "Info",
      props:{
        data:''
      },
      methods:{
        newPage(){
          const old = this.$router.resolve({
            path: 'infoDetail',
            query: {
              id:this.data.id
            }
          });
          // console.log(this.data.company_id);
          window.open(old.href, '_blank')
        }
      }
    }
</script>

<style scoped lang="scss">
  @import "../../../static/scss/public.scss";

  .info{
    border-bottom: 1px solid #E0E0E0;
    padding:15px 0 10px 0;

    &:hover{
      background: #eee;
      cursor: pointer;

      a{
        color: $main-color;
      }
    }

    h3{
      display: flex;
      justify-content: space-between;
      margin-bottom: 10px;

      a{
        font-size: 15px;
        color: #464646;display: -webkit-box;overflow: hidden;text-overflow: ellipsis;word-wrap: break-word;white-space: normal!important;-webkit-line-clamp: 1;-webkit-box-orient: vertical;
        width: 70%;

      }
      span{
        width: 20%;
        font-size: 13px;
        color: #9D9D9D;
        text-align: right;
      }
    }
    .infoBar{

      .ivu-tag{
        margin-right: 44px;
        background: none;
        color: #64CD9E;
        border-color: #64CD9E;

      }
      >span{
        margin-right: 44px;
        display: inline-block;
        color: #676767;
        font-size: 13px;
        line-height: 22px;

      }
    }
  }
</style>



