<template>
  <div class="infoDetail pt72">
    <div class="pageWidth">
      <Site :site="site"></Site>

      <div class="content">
        <h1>{{data.title}}</h1>
        <p>时间：{{data.create_time}}</p>
        <p>来源：{{data.from}}</p>

        <h2>概要</h2>
        <h3>公告信息：</h3>
        <div class="font">
          <p v-for="a,index in data.announcement" :key="a.id">
            <b>{{a.key}}:</b>{{a.value}}
          </p>
        </div>

        <h3>联系人及联系方式：</h3>
        <div class="font">
          <p v-for="g,index in data.link" :key="g.id">
          <b>{{g.key}}:</b>{{g.value}}
        </p>
        </div>

        <h3>附件：</h3>
        <div class="font">
          <p v-for="f,index in data.attachment" :key="f.id">
          附件{{index+1}}：
          <a :href="f.url" target="_blank">{{f.name}}</a>
        </p>
        </div>

        <h2>正文</h2>

        <div class="about" :class="{more:!showMore}"></div>


        <div class="loadMore" @click="loadMore()" v-if="showMore">点击加载更多</div>

      </div>
    </div>
  </div>
</template>

<script>

  import Vue from 'vue';
  import { Tag ,Icon } from 'iview';
  Vue.component('Icon',Icon);

  import Site from '../block/Site'

  // 数据获取；
  import request from '../../../static/js/request'
  Vue.use(request);

  export default {
    name: "InfoDetail",
    components:{
      Site
    },
    data(){
      return{
        showMore:false,
        about:'',
        data:'',
        d:{
          'id':this.$route.query.id
        }
        ,site:[
          {
            title:'招标信息',
            src:'/infoList'
          },
          {
            title:'招标详情',
            src:''
          }
        ]
        ,goodList:''
      }
    },
    methods:{
      query (el) {
        if (typeof el === 'string') {
          var selector = el;
          el = document.querySelector(el);
          if (!el) {
            "development" !== 'production' && warn(
              'Cannot find element: ' + selector
            );
            return document.createElement('div')
          }
        }
        return el
      }
      ,loadMore(){
        this.$set(this , 'showMore' ,false );
      }
    },
    created(){
      var _this = this;
      console.log(_this.d);
      request.getDataPackage('Procurement/getProcurementInfo',_this.d,function (data) {
        console.log(data.data );
        _this.$set(_this , 'data' ,data.data );
        _this.query('.about').innerHTML = _this.data.content;
        var aboutH = _this.query('.about').offsetHeight;
        // console.log(aboutH);
        if(aboutH>1000){
          _this.$set(_this , 'showMore' ,true );
        }
      });
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../static/scss/public.scss";

  .infoDetail{
    background: #f4f4f4;
    padding-bottom: 150px;
  }
  .content{
    background: #fff;
    padding: 50px;
    line-height: 30px;
    font-size: 15px;

    h1{
      font-size:27px;
      font-family:MicrosoftYaHei-Bold;
      font-weight:bold;
      color:rgba(70,70,70,1);
      margin-bottom: 50px;
    }
    p{
      font-size: 15px;
    }
    h2{
      font-size:23px;
      font-family:MicrosoftYaHei;
      font-weight:400;
      color:rgba(51,51,51,1);
      position: relative;
      padding:40px 20px;

      &:after{
        position: absolute;
        content: '';
        border-bottom: 8px solid transparent;
        border-top: 8px solid transparent;
        border-left: 10px solid #2787F4;
        left: 0;
        top: 50%;
        margin-top: -7px;
      }
    }
    h3{
      font-size:17px;
      font-family:MicrosoftYaHei-Bold;
      font-weight:bold;
      color:rgba(70,70,70,1);
      line-height:30px;
      padding-left: 20px;
      padding-bottom: 10px;
    }
    .font{
      padding-left: 20px;
      margin-bottom: 30px;

      b{
        display: inline-block;
        margin-right: 20px;
      }
    }
    .about{
      padding-left: 20px;
      max-height: 1000px;
      overflow: hidden;

      &.more{
        max-height: inherit;
      }
    }
  }


</style>



