<template>
  <div class="needDetail pt72">
    <div class="pageWidth">
      <Site :site="site"></Site>

      <div class="left">

        <div class="left-top">
          <h2 v-if="data.demand_content">{{data.demand_content}}</h2>
          <span class="time">{{data.time}}</span>
          <a class="report" href="">举报</a>
          <img v-for="i,index in data.demand_img" :key="index" :src="i.img_url" alt="">
        </div>

        <Title title="参与评论"></Title>
        <div class="message-div">
          <div class="message">
            <p>
              <input type="text" placeholder="发表评论" v-model="replyArr.content">
            </p>
            <a href="javascript:;" @click="reply()">评 论</a>
          </div>
        </div>

        <Title title="热门评论"></Title>
        <div class="messageAll" v-if="comment_list.length">
          <MessageOne v-if="showMore" v-for="m in 1" :key="m.id" :data="comment_list[m-1]"></MessageOne>
          <MessageOne v-else v-for="m in comment_list" :key="m.id" :data="m"></MessageOne>
          <div class="loadMore" v-if="showMore" @click="showMoreFun()">点击加载更多</div>
        </div>
      </div>

      <div class="right">

        <div class="head gray" v-if="data.reg_type==1">
          <img :src="data.member_header" alt="">
          <h3>{{data.member_alex}}</h3>
          <p>未认证</p>
        </div>

        <div class="head" v-else>
          <img :src="data.member_header" alt="">
          <h3>{{data.member_alex}}</h3>
          <p>{{data.sub_name}}</p>
        </div>

        <ul v-if="data.reg_type!=1">
          <li>
            <b>所在区域：</b>
            <span>{{data.province_name}}</span>
          </li>
          <li>
            <b>主营业务：</b>
            <span>{{data.com_service}}</span>
          </li>
          <li>
            <router-link :to="'companyDetail?id=' + data.com_id" class="btn">
              <button>查看企业 <Icon type="ios-arrow-round-forward" /></button>
            </router-link>
          </li>
        </ul>
      </div>

    </div>
  </div>
</template>

<script>
  import Vue from 'vue';
  import { Icon } from 'iview';
  Vue.component('Icon',Icon);

  // 数据获取；
  import request from '../../../static/js/request'
  Vue.use(request);

  import Site from '../block/Site'
  import Title from '../block/Title'
  import MessageOne from '../block/MessageOne'

  export default {
    name: "NeedDetail"
    ,components:{
      Site ,Title ,MessageOne
    }
    ,data(){
      return{
        data:'',
        messageNub:3,
        showMore:true
        ,list:{
          lis:'',
          total:1
        },
        site:[
          {
            title:'需求',
            src:'need'
          },
          {
            title:'需求详情',
            src:''
          }
        ],
        d:{
          'id':this.$route.query.id
        },
        label:['' ,'' ,'']
        ,comment_list:''
        ,replyArr:{
          content:'',
          type_id:'',
          type:7
        }
      }
    }
    ,methods:{
      reply(){
        console.log(this.replyArr);

        var _this = this;
        request.getDataPackage('Demand/addComment',_this.replyArr,function (data) {
          console.log(data);
          _this.comment_list.unshift(data.data);
          _this.$set(_this , 'comment_list' ,_this.comment_list );
          _this.$set(_this , 'replyArr' ,'' );
        });

      },
      showMoreFun(){
        this.showMore = false;
      }
    }
    , created(){
      var _this = this;
      request.getDataPackage('Demand/getDemandInfo',_this.d,function (data) {
        console.log(data);
        _this.$set(_this , 'data' ,data.data );
        _this.$set(_this , 'comment_list' ,data.data.comment_list );
        _this.$set(_this.replyArr , 'type_id' ,data.data.id );
        console.log(_this.comment_list);
        // if(data.data.count){
        //   _this.$set(_this.list , 'total' ,data.data.count*1 );
        // }
      });
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../static/scss/public.scss";

  .needDetail {
    background: #f4f4f4;
    overflow: hidden;
    padding-bottom: 120px;


    .left {
      width: 860px;
      background: rgba(255, 255, 255, 1);
      float: left;
      padding-top: 30px;
      padding-bottom: 50px;

      .left-top{
        position: relative;
        padding-left: 48px;

        h2{
          color: #464646;
          font-size: 17px;
          margin-right: 170px;
          margin-bottom: 20px;
          font-weight: normal;

          &:after{
            position: absolute;
            content: '';
            width:7px;
            height:7px;
            background:rgba(196,196,196,1);
            @include border-radius(50%);
            left: 28px;
            top: 8px;
          }
        }
        .time{
          font-size:13px;
          font-weight:400;
          color:rgba(157,157,157,1);
          position: absolute;
          right: 30px;
          top:0;
        }
        .report{
          width:37px;
          height:16px;
          font-size:17px;
          font-family:MicrosoftYaHei;
          font-weight:400;
          color:rgba(157,157,157,1);
          line-height:24px;
          position: absolute;
          right: 30px;
          top:20px;
        }
        img{
          margin: 0 10px 10px 0;
          max-width: 420px;
        }
      }

      .title{
        margin-left: 34px;
        margin-top: 72px;
      }
      .message-div{
        margin:30px 170px 0 48px;
      }
      .messageAll{
        margin-right: 166px;
        margin-left: 50px;
        margin-top: 30px;

        .messageOne:after{
          position: absolute;
          right: 0;
          bottom: 0;
          left: 80px;
          content: '';
          border-bottom: 1px solid #F4F4F4
        }
      }

      .loadMore{
        width: 524px;
        margin-left: 120px;
        margin-top: 24px;
      }

    }


    .right {
      width: 470px;
      background: rgba(255, 255, 255, 1);
      float: right;
      padding: 30px;

      .head{
        font-size:17px;
        font-family:MicrosoftYaHei;
        font-weight:400;
        position: relative;
        padding-left: 90px;
        min-height: 72px;

        img{
          width:72px;
          height:72px;
          @include border-radius(8px);
          position: absolute;
          left: 0;
          top: 0;
        }
        h3{
          color:rgba(51,51,51,1);
          margin-bottom: 14px;
        }
        p{
          color:rgba(103,103,103,1);
          position: relative;
          padding-left: 12px;

          &:after{
            width:6px;
            height:6px;
            background:rgba(39,135,244,1);
            @include border-radius(50%);
            position: absolute;
            content: '';
            left: 0;
            top: 50%;
            margin-top: -3px;
          }
        }
      }

      .gray{

        p{
          color: #cec8c8;

          &:after{
            background-color: #cec8c8;
          }
        }
      }

      ul{
        font-size: 15px;
        padding-top: 30px;

        li{
          display: flex;
          justify-content: space-between;
          margin-bottom: 10px;

          b{
            width: 80px;
            display: inline-block;
          }
          span{
            width: 315px;
            display: inline-block;
            word-wrap: break-word;
          }

          .btn{
            margin:30px auto 0;

            i{
              font-size: 30px;
              vertical-align: middle;
            }
          }

        }
      }

      .btn{

        button{
          width:210px;
          line-height:40px;
          text-align: center;
          border: none;
          font-size:15px;
          background:rgba(39,135,244,1);
          color: #fff;
          cursor: pointer;

          &.gray{
            background: #999;
            cursor: default;
          }
        }
      }

    }

  }

</style>



