<template>
  <div class="newList pt72">
    <div class="pageWidth">
      <Site :site="site"></Site>

      <!--标题-->
      <div class="tab">
        <a href="javascript:;" @click="mouseOver(index)" v-for="n,index in news" :class="{active: index == now }">{{n.title}}</a>
      </div>

      <!--内容-->
      <div class="new-tab-content">

        <ul class="new-tab-content-ul" v-if="now == 0">
          <li v-for="n,index in list.lis" :key="index" class="newsitem" :class="{ 'inform':n.type == '企业' ,'event':n.type == '需求' ,'amusement':n.type == '活动' ,'tutorial':n.type == '产品'}" @click="detailAll(n.type ,n)">
            <span class="item-type">{{n.type}}</span>
            <a class="item-href" target="_blank" >{{n.title}}</a>
            <span class="item-time">{{n.time}}</span>
          </li>
        </ul>

        <ul class="new-tab-content-ul" v-if="now == 1">
          <li v-for="n,index in list.lis" :key="index" class="newsitem tutorial" @click="detail('goodDetail' ,n.goods_id)">
            <span class="item-type">产品</span>
            <a class="item-href" target="_blank" >{{n.goods_name}}</a>
            <span class="item-time">{{n.time}}</span>
          </li>
        </ul>

        <ul class="new-tab-content-ul" v-if="now == 2">
          <li v-for="n,index in list.lis" :key="index" class="newsitem inform" @click="detail('companyDetail' ,n.company_id)">
            <span class="item-type">企业</span>
            <a class="item-href" target="_blank" >{{n.com_name}}</a>
            <span class="item-time">{{n.time}}</span>
          </li>
        </ul>

        <ul class="new-tab-content-ul" v-if="now == 3">
          <li v-for="n,index in list.lis" :key="index" class="newsitem" @click="detail('infoDetail' ,n.id)">
            <span class="item-type">招标</span>
            <a class="item-href" target="_blank" >{{n.title}}</a>
            <span class="item-time">{{n.time}}</span>
          </li>
        </ul>

        <ul class="new-tab-content-ul" v-if="now == 4">
          <li v-for="n,index in list.lis" :key="index" class="newsitem event" @click="detail('needDetail' ,n.id)">
            <span class="item-type">需求</span>
            <a class="item-href" target="_blank" >{{n.demand_content}}</a>
            <span class="item-time">{{n.time}}</span>
          </li>
        </ul>

        <ul class="new-tab-content-ul" v-if="now == 5">
          <li v-for="n,index in list.lis" :key="index" class="newsitem amusement" @click="detail('activityDetail' ,n.id)">
            <span class="item-type">活动</span>
            <a class="item-href" target="_blank" >{{n.act_name}}</a>
            <span class="item-time">{{n.time}}</span>
          </li>
        </ul>

      </div>

      <Page @on-change="onChange" class="pageNext" :current="d.page" :total="list.total" :page-size="d.limit"  prev-text="上一页" next-text="下一页" />

    </div>
  </div>
</template>

<script>

  import Vue from 'vue';
  import { Page ,Icon } from 'iview';
  Vue.component('Page',Page);
  Vue.component('Icon',Icon);

  // 数据获取；
  import request from '../../../static/js/request'
  Vue.use(request);

  import Site from '../block/Site'
  import Tab from '../block/Tab'

  export default {
    name: "NewList",
    data(){
      return{
        url:'Home/newList',
        list:{
          lis:'',
          total:1
        },
        now:'0',
        d:{
          'limit':15,
          'page': 1,
          'type':'0'
        },
        tab:''
        ,site:[
          {
            title:'最新',
            src:''
          }
        ]
        , news:[
          {
            title:'推荐',
            content:''
          },
          {
            title:'产品',
            content:''
          },
          {
            title:'企业',
            content:''
          },
          {
            title:'招标',
            content:''
          },
          {
            title:'需求',
            content:''
          },
          {
            title:'活动',
            content:''
          }
        ]
      }
    },
    components:{
      Site ,Tab
    }
    ,methods:{

      detailAll(type ,n){
        var link = '' ,ids='';
        switch (type) {
          case '产品':
            link = 'goodDetail';
            ids = n.goods_id;
            break;
          case '企业':
            link = 'companyDetail';
            ids = n.id;
            break;
          case '招标':
            link = 'infoDetail';
            ids = n.id;
            break;
          case '需求':
            link = 'needDetail';
            ids = n.id;
            break;
          case '活动':
            link = 'activityDetail';
            ids = n.id;
            break;
        }
        const old = this.$router.resolve({
          path: link,
          query: {
            id:ids
          }
        });
        window.open(old.href, '_blank')
      },
      detail(link,id){
        const old = this.$router.resolve({
          path: link,
          query: {
            id:id
          }
        });
        window.open(old.href, '_blank')
      },
      onChange(e){
        var _this = this;
        _this.$set(_this.d , 'page' ,e*1 );
        request.getDataPackage(_this.url,_this.d,function (data) {
          console.log(data);
          _this.$set(_this.list ,'lis' ,data.data.list);
        })
      },
      mouseOver(idx){
        if(this.now == idx){
          return false;
        }
        this.d.type = this.now = idx;

        let _this = this;
        _this.$set(_this.d , 'page' ,1 );
        request.getDataPackage(_this.url,_this.d,function (data) {
          console.log(data);
          _this.$set(_this.list ,'lis' ,data.data.list);
          _this.list.total = data.data.count*1;
        })
      }
      , newPage(link){
        const old = this.$router.resolve({
          path: link
        });
        console.log(old);
        window.open(old.href, '_blank')
      }
    }
    , created(){
      let _this = this;
      request.getDataPackage(_this.url,_this.d,function (data) {
        console.log(data);
        _this.$set(_this.list ,'lis' ,data.data.list);
        _this.list.total = data.data.count*1;
      })
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../static/scss/public.scss";

  .tab{
    border-bottom: 1px solid #EAEAEA;
    margin-top: 10px;
    position: relative;
    height: 50px;
    overflow: hidden;

    a{
      display: inline-block;
      padding: 13px 0;
      margin-right: 60px;
      font-size:15px;
      font-weight:400;
      color:rgba(70,70,70,1);
      position: relative;

      &.active{
        color: $main-color;
        font-weight: bold;

        border-bottom: 2px solid $main-color;
      }
    }

  }

  .newList{
    background: #f4f4f4;
  }
  .new-tab-content{
    margin-bottom: 40px;
  }
  .pageNext{
    text-align: center;
    padding-bottom: 120px;
    padding-top: 30px;

  }


  .new-tab-content-ul{position:relative;margin-top: 11px;}

  .news-type-list{box-sizing:border-box;overflow:hidden;padding:20px 40px 0 40px;position:relative}
  .news-type-list>.newsitem{width:100%;height:61px;line-height:60px;overflow:hidden;border-top:none;border-bottom:1px #eff1f1 solid}
  .news-type-list>.newsitem>.item-href{width:660px;height:60px}
  .news-type-list>.newsitem>.item-time{width:120px;height:60px;font-size:14px;letter-spacing:1px;margin-left:20px}

  .newsitem{list-style:none;height:40px;line-height:39px;overflow:hidden;box-sizing:border-box;border-bottom:1px #e0e2e2 solid;text-align:left;font-size:0}
  .newsitem>.item-type{display:inline-block;vertical-align:middle;*zoom:1;*display:inline;width:36px;height:20px;box-sizing:border-box;font-size:12px;border:1px #69c5d2 solid;color:#1da6ba;line-height:18px;text-align:center; @include border-radius(3px);}
  .newsitem>.item-href{display:inline-block;vertical-align:middle;*zoom:1;*display:inline;width:1150px;height:39px;overflow:hidden;margin-left:20px;color:#424242;font-size:14px;letter-spacing:1px;text-overflow:ellipsis;white-space:nowrap}
  .newsitem>.item-href:hover{color:#1da6ba;text-decoration:none}
  .newsitem>.item-time{display:inline-block;vertical-align:middle;*zoom:1;*display:inline;width:120px;height:39px;overflow:hidden;text-align:right;font-size:14px;letter-spacing:1px;color:#9d9d9d;margin-left:18px}
  .newsitem.inform>.item-type{color:#bb9a6c;border:1px #c5ab86 solid;}
  .newsitem.inform>.item-href:hover{color:#bb9a6c}
  .newsitem.event>.item-type{color:#6388c5;border:1px #97afc5 solid;}
  .newsitem.event>.item-href:hover{color:#6388c5}
  .newsitem.amusement>.item-type{color:#e9852d;border:1px #e9852d solid;}
  .newsitem.amusement>.item-href:hover{color:#e9852d}
  .newsitem.tutorial>.item-type{color:#4ba36a;border:1px #4ba36a solid}
  .newsitem.tutorial>.item-href:hover{color:#4ba36a}
</style>



