import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router);

import Content from '../components/index/Contents'
import About from '../components/about/About'
import Contact from '../components/contact/Contact'
import Down from '../components/Down'
import Activity from '../components/activity/Activity'
import ActivityDetail from '../components/activity/ActivityDetail'
import ActivityPay from '../components/activity/ActivityPay'
import Company from '../components/company/Company'
import CompanyDetail from '../components/company/CompanyDetail'
import Goods from '../components/goods/Goods'
import GoodDetail from '../components/goods/GoodDetail'
import InfoList from '../components/info/InfoList'
import InfoDetail from '../components/info/InfoDetail'
import NewList from '../components/new/NewList'
import Need from '../components/need/Need'
import NeedDetail from '../components/need/NeedDetail'
import My from '../components/my/My'
import EnterpriseCertification from '../components/company/EnterpriseCertification'

// 二级路由；
import AccountInformation from '../components/my/AccountInformation'
import MyActivity from '../components/my/MyActivity'
import MyNeed from '../components/my/MyNeed'
import MyProductList from '../components/my/MyProductList'
import MyProductListDetail from '../components/my/MyProductListDetail'
import MyProductInfo from '../components/my/MyProductInfo'
import CaseInfo from '../components/my/CaseInfo'
import MyActivityCode from '../components/my/MyActivityCode'
import Enterprises from '../components/Enterprises'

export default new Router({
  routes: [
    { path: '/', name: 'content', component: Content },
    { path: '/about', name: 'about', component: About },
    { path: '/contact', name: 'contact', component: Contact },
    { path: '/down', name: 'down', component: Down },
    { path: '/activity', name: 'activity', component: Activity },
    { path: '/activityDetail', name: 'activityDetail', component: ActivityDetail },
    { path: '/activityPay', name: 'activityPay', component: ActivityPay },
    { path: '/company', name: 'company', component: Company },
    { path: '/companyDetail', name: 'companyDetail', component: CompanyDetail },
    { path: '/goods', name: 'goods', component: Goods },
    { path: '/goodDetail', name: 'goodDetail', component: GoodDetail }
    , { path: '/infoList', name: 'infoList', component: InfoList }
    , { path: '/infoDetail', name: 'infoDetail', component: InfoDetail }
    , { path: '/newList', name: 'newList', component: NewList }
    , { path: '/need', name: 'need', component: Need }
    , { path: '/needDetail', name: 'needDetail', component: NeedDetail }
    , { path: '/enterpriseCertification', name: 'enterpriseCertification', component: EnterpriseCertification }
    , { path: '/enterprises', name: 'enterprises', component: Enterprises }
    , { path: '/my', name: 'my', component: My
        ,children:[
          { path: '/accountInformation', name: 'accountInformation', component: AccountInformation }
        ,{ path: '/myActivity', name: 'myActivity', component: MyActivity }
        ,{ path: '/myNeed', name: 'myNeed', component: MyNeed }
        ,{ path: '/myProductList', name: 'myProductList', component: MyProductList }
        ,{ path: '/myProductListDetail', name: 'myProductListDetail', component: MyProductListDetail }
        ,{ path: '/myProductInfo', name: 'myProductInfo', component: MyProductInfo }
        ,{ path: '/caseInfo', name: 'caseInfo', component: CaseInfo }
        ,{ path: '/myActivityCode', name: 'myActivityCode', component: MyActivityCode }
        ]
      }
  ]
  // , mode:'history'
})



